import { stringUtils } from "@ehas/utils";
import { bankCardValidate, isPhoneCardFrame } from "./account";
import { isNotBulletFrame, checkPhoneNo, isNewStartup } from "./check";
import { getUrlParam } from "./https";
import { getAppWaitTime } from "./login";
import { toNumVersion } from "./format";
import { setHeadOption } from "../js/titleUtil";
import memnu from "~/common/datas/menu.js";
import { ViewBase } from "@ehas/core";
/**
 * 
 * @param {*} time 
 */
function setAppWaitTime(time) {
    App.local.remove("m_app_time");
    App.local.set("m_app_time", time);
}

/**
 * ,key"_appParams"
 * @param {*} val 
 */
function setInfo(val) {
    window._appParams = val;
}

/**
 * 
 */
function onPageResume(data) {
    let isUnionPayBackFlag = App.storage.get("isUnionPayBackFlag");
    if (isUnionPayBackFlag) {
        App.storage.remove("isUnionPayBackFlag");
        App.jsApi.off("onUnionpayScanQRCode");
        App.jsApi.off("onUnionpaySn");
        App.jsApi.unionpayLoginout({ loginoutCode: "0002" });
    }
    // 
    if (data.result && data.result.menuID) {
        App.menu.go(data.result.menuID);
    }
}

/**
 * 
 * 1.getClipboardText
 * 2.
 * 3.
 */
async function getClipboardText() {
    //
    if (!isNotBulletFrame()) return;
    // 
    const phoneFlag = await App.local.get("m_clip_phone_flag");
    // 
    const cardFlag = await App.local.get("m_clip_card_flag");
    // 
    const currClipObj = await App.jsApi.getCopyText();
    // 
    const currClip = currClipObj.result.text.replace(/\s/g, "");
    // ,
    if (stringUtils.isStrEmpty(currClip)) return;
    // 
    const lastClip = await App.local.get("m_clipboard_text");
    // 
    if (lastClip === currClip) return;
    // 
    App.local.set("m_clipboard_text", currClip);
    //   
    if (phoneFlag && checkPhoneNo(currClip)) {
        //
        isPhoneCardFrame("phoneNo", currClip);
    } else if (cardFlag && bankCardValidate(currClip)) {
        // 
        isPhoneCardFrame("card", currClip);
    } else if (App.bank.flags.common?.isCanCopySearch && isNotBulletFrame(["FloorHome/Home/Index"])) {
        //app 81010
        let copySearchReg = /^([\u4e00-\u9fa5]){1,10}$/;
        if (copySearchReg.test(currClip)) {
            // {chnlPgCd 1 ; productName }
            window.$G.goToNext("#Search/Search/Search", { chnlPgCd: "1", productName: currClip });
        }
    }
}

/*
 * 
 */
async function CopyTextCheck() {
    let copyText = await App.jsApi.getCopyText();
    let text = copyText.result.text;
    if (text?.split(":")[0] === `sccba${App.bank.flags.common.bankId}`) {
        const uri = getUrlParam(text, "uri");
        let param = getUrlParam(text, "param");
        const jumpTyp = getUrlParam(text, "jumpTyp");
        const paramType = getUrlParam(text, "paramType");
        if (jumpTyp == "3" && paramType == "1") {
            let rsp = await memnu.findById(uri);
            try {
                param = JSON.parse(param);
                ViewBase.prototype.$alarmWord({
                    custNm: decodeURIComponent(param.custNm),
                    pdNm: decodeURIComponent(param.pdNm),
                    toDetail: () => {
                        window.$G.goToNext(rsp.url, param);
                    }
                });
            } catch (err) {
                window.$G.logErrorWithCode("common/utils/device.jsparam", param);
            }
        }
        App.jsApi.copyText({ text: "" });
    }
}

/**
 * 
 * @param {*} key card:,phone:
 * @param {*} flag true:,false:
 */
function setClipboardFlag(key, flag) {
    if (key === "card") {
        // 
        if (flag) {
            //
            App.local.set("m_clip_card_flag", true);
        } else {
            //
            App.local.set("m_clip_card_flag", false);
        }
    } else {
        // 
        if (flag) {
            //
            App.local.set("m_clip_phone_flag", true);
        } else {
            //
            App.local.set("m_clip_phone_flag", false);
        }
    }
}

/**
 * 
 * @param {*} obj 
 * {
 *    backRouter:""//
 *    exitFlag:false //
 *    exitWithWebviewFlag:false// webview
 * }
 */
function goBack(obj) {
    const { backRouter, exitWithWebviewFlag = false } = obj;
    if (exitWithWebviewFlag) {
        // webView
        App.jsApi.closeWebContainer();
    } else {
        if (backRouter) {
            window.$G.goToNext(backRouter);
        } else {
            App.router.back();
        }
    }
}

/**
 * 
 * compareVersion type:   
 * menu  
 */
function clientVersionCom(compareVersion) {
    //
    const clientVersion = App.jsApi.getAppVerion();
    const nowNumVersion = toNumVersion(clientVersion);
    if (nowNumVersion < toNumVersion(compareVersion)) {
        App.getViewInstance("content").$confirm({
            message: ``,
            sureText: ``,
            cancelText: "",
            modalId: "sessionOut",
            closed: isSure => {
                if (isSure) {
                    //
                    window.$G.goToNext("#Setting/AboutUs/Index");
                }
            }
        });
    }
}

/**
 * 
 */
function faceVerification(actSeq) {
    //
    App.storage.remove("faceVerification");
    App.storage.set("faceVerification", {
        busiSceneNo: App.globalFlag?.busiSceneNo,
        actSeq: actSeq
    });
}
// 
// modalId
function useAppLaunchPopDialog(options, type) {
    if (type === "alert") {
        App.getViewInstance("content").$alert(options);
    } else if (type === 'confirm') {
        App.getViewInstance("content").$confirm(options);
    }
}

//  tabbar
function getTabbarHeight() {
    let height = 0;
    if(isNewStartup()) {
        const userAgent = navigator.userAgent.replace(/\s/g, "");
        height = userAgent.match(/@tabbarHeight\((\d+)\)/)[1];
    }
    return height;
}

function setWai () {
    App.jsApi.getSuitableState().then(res => {
        if (res.result.state) {
            App.jsApi.setSuitableState({ "state": res.result.state }).then(() => {
                window.$wai?.Start()
            }).catch(e => window.$G.jsApiErrorMsg(e));
        } else {
            App.jsApi.setSuitableState({ "state": false }).then(() => {
                window.$wai?.Stop();
            }).catch(e => window.$G.jsApiErrorMsg(e));

        }
    })
}
// 
function closeMirrorView() {
    // 
    if(isNewStartup()) {
        App.jsApi.jsapiExistInApp({ jsApiList: ["closeCurrentMirrorView"] }).then(res => {
            // closeCurrentMirrorView
            if (res?.result?.closeCurrentMirrorView) {
                App.jsApi.closeCurrentMirrorView();
            }
        })
    }
}
export {
    setAppWaitTime,
    setInfo,
    onPageResume,
    getClipboardText,
    CopyTextCheck,
    setClipboardFlag,
    goBack,
    clientVersionCom,
    faceVerification,
    getAppWaitTime,
    setHeadOption,
    useAppLaunchPopDialog,
    getTabbarHeight,
    setWai,
    closeMirrorView
};
