    /**
     * 
     * @param {*} param0 sourceNo, chnlPdNo
     */
    static queryBookList({ sourceNo, chnlPdNo, inqFlg, type } = {}) {
        return App.requester
            .request("fin/findetail/finibkatchinflistqry.biz", {
                data: { sourceNo, chnlPdNo, inqFlg, type }
            })
            .then(rsp => {
                let bookList = [];
                let TransRules = {};
                let Instructions = {};
                for (let item of rsp.ibkAtchInfEntityList) {
                    if (["01", "02", "03", "04", "05", "07", "30"].includes(item.pdAtchTyp)) {
                        if (item.pdAtchTyp == "02") {
                            Instructions = item;
                        }
                        //'01','02','03','04','05'()
                        bookList.push(item);
                    } else if (item.pdAtchTyp == "06") {
                        //'06'
                        TransRules = item;
                    }
                }
                return Object.assign(rsp, {
                    ibkAtchInfEntityList: bookList,
                    TransRules,
                    Instructions
                });
            });
    }

    /**
     * 
     * @param {*} param0 urltitle
     */
    static previewAgreement({ url, title, hasConfirmed, useJsApi } = {}) {
        url = transferUrl(url);
        //html
        if (/(.htm|.html)$/.test(url)) {
            window.$G.goToNextRetain({
                router: `#${App.bank.flags?.finance?.module || "Finance"}/Buying/HtmAgreement`,
                param: {
                    url,
                    title
                }
            });
        } else {
            //jsapi,
            if (useJsApi) {
                return App.jsApi.previewPdf({
                    url,
                    title
                });
            } else {
                //$previewPdf,
                return App.getViewInstance("content").$previewPdf({
                    url, // pdf
                    title, // 
                    hasConfirmed
                });
            }
        }
    }

    /**
     * 
     * @param {*} dealNoCard false
     */
    static finGetPayAcctList(dealNoCard = false) {
        return App.requester
            .request("fin/acctlist/acctlistqry.biz", {
                data: {
                    txCd: "FIN000101",
                    acctAuthFltFlg: "Y",
                    acctStsFltFlg: "N"
                }
            })
            .then(rsp => {
                rsp.LIST = rsp.acctListQryDtoEntitys;
                if (rsp.LIST.length == 0 && dealNoCard) {
                    App.getViewInstance("content").$confirm({
                        title: "",
                        message: "",
                        sureText: "",
                        closed: isSure => {
                            if (isSure) {
                                addCard();
                            }
                        }
                    });
                }
                return rsp;
            });
    }

    /**
     * ,truefalse
     * @param {*} acctNo 
     * @param {*} cancel 
     */
    static checkTakingDown(acctNo, cancel = null) {
        return FinanceUtil.finGetPayAcctList().then(rsp => {
            let acctIndex = rsp.LIST.findIndex(item => {
                return item.acctNo == acctNo;
            });

            if (acctIndex == -1) {
                //
                App.getViewInstance("content").$confirm({
                    message: "",
                    sureText: "",
                    cancelText: "",
                    closed: isSure => {
                        if (isSure) {
                            //
                            fastTakingDown({ acctNo });
                        } else {
                            cancel && cancel();
                        }
                    }
                });
                return false;
            } else {
                return true;
            }
        });
    }

    /**
     * ,true,false
     */
    static async checkOpenFTS() {
        //
        var isOpenFTS = App.storage.get("isOpenFTS");
        if (isOpenFTS !== undefined) {
            return isOpenFTS;
        } else {
            let loginUserNm = (await getloginUserNm()) || "";
            //,
            if (!App.bank.flags.finance.ifOpenFinanceTransfer) return false;
            //,
            return App.requester
                .request("wlh/common/switchquery.biz", {
                    data: {
                        txCd: "FIN000702",
                        loginUserNm
                    }
                })
                .then(rsp => {
                    let openFlg = rsp.svSwch == "Y";

                    //
                    App.storage.remove("isOpenFTS");
                    App.storage.set("isOpenFTS", openFlg);
                    return openFlg;
                });
        }
    }
