import { isLogin } from "./check";
/**
 * jsapi
 * @param {*} res 
 * @param {*} type 
 */
function dealDeviceAuth(res, type) {
    //
    // EPFJ005S toast,
    //EPFN334B:
    if (
        ![
            "EPFN335B",
            "EPFN336B",
            "EPFN337B",
            "EPFN338B",
            "EPFN339B",
            "EPFN333B",
            "EPFN330B",
            "EPFJ005S",
            "EPFN334B",
            "EPFN839T", // 
            "EPFN843T", //  
            "EPFN844T" //  
        ].includes(res.code)
    ) {
        //EPFN306B  
        if (res.code == "EPFN306B") {
            deviceAuthorTips(type);
        } else {
            //toast
            if (res.cvrtMsg) {
                // 
                App.getViewInstance("content").$toast(res.cvrtMsg);
            }
        }
    }
}
/**
 * 
 * type-"1": "",//
 * "2": "/",//
 * "3": "",//
 * "4": "",//
 * "5": ""//
 */
function deviceAuthorTips(type) {
    App.getViewInstance("content").$confirm({
        message: `${App.dict.get("f-deviceAuthorTipCd", type)}`,
        sureText: "",
        cancelText: "",
        closed: isSure => {
            if (isSure) {
                App.jsApi.openAppSetting();
            }
        }
    });
}

/**
 * ,
 * CR   ECR36   EF506
 * SA   E1234    E1234
 * @param {*} code 
 */
function errorCode(code) {
    if (code == "ECR36" || code == "E1234" || code == "EF506") {
        return true;
    } else {
        return false;
    }
}

/**
 * 
 * @param {*} riskException 
 * riskException:{code,orignMsg,cvrtMsg}
 */
function dealRiskException(riskException, fidoFlag = false,resultType) {
    // 
    if (fidoFlag && riskException?.code && riskException?.orignMsg) {
        setErrorMsg(riskException, fidoFlag);
    }
    //,:801
    if (!fidoFlag && App.bank.flags?.account?.gotoFaceResult) {
        // EPFN812TEPFN843TEPFN805TEPFN811TEPFN821T
        let errorCode = ["EPFN812T", "EPFN843T", "EPFN805T", "EPFN811T", "EPFN821T"];
        // 
        if (!errorCode.includes(riskException?.code)) {
            window.$G.goToNextRetain({ router: "#Account/Result/FaceCertifyResult", param: { pageFlag: "Y",resultType:resultType } });
        }
    }
    // 
    if (!riskException || !riskException.code || !riskException.detailCode) {
        window.$G.logWarnWithCode(
            "common/utils/error.js",
            "usr/login/speclgnerrinfrcd.biz" + JSON.stringify(riskException)
        );
        return;
    }
    setErrorMsg(riskException, fidoFlag);
}
/**
 * 
 * */

async function setErrorMsg(riskException, fidoFlag) {
    // 
    const loginUserNm = (await App.local?.get("loginUserNm")) || (await App.storage.get("loginUserNmShow")) || "";
    // 
    let requestData = {
        faceIdenRsltCd: fidoFlag ? riskException.code : riskException.detailCode,
        faceIdenRsltMsg: riskException.orignMsg,
        busnTyp: fidoFlag ? 19 : "", //fido19
        errCdSubCd: riskException.childFaceCode, //code,
        loginUserNm: loginUserNm //
    };
    // 
    App.requester
        .request("usr/login/speclgnerrinfrcd.biz", {
            data: requestData,
            useDefaultError: false,
            slient: true
        })
        .catch(res => {
            window.$G.logWarnWithCode(
                "common/utils/error.js",
                "usr/login/speclgnerrinfrcd.biz" + JSON.stringify(res)
            );
        });
}

