/* eslint-disable */
import { updateUserInfo } from "./user";
import { downloadPic } from "./https";
import { isLogin } from "./check";
import { WapWeChatUtil } from "~/common/js/wap";
import { loginUtil } from "~/common/js/login";
import { ViewBase } from "@ehas/core";
/**
 * url
 * @param {*} url ()
 * @param {*} param 
 * @param {*} type (1:6.0,url/param;2:6.0,url,param;3:6.0)
 * @param {*} selfRefreshFlag true false 
 */
async function goToLogin(url, param, config) {
    if (WapWeChatUtil.isWXBusiness()) {
        WapWeChatUtil.dealWXLogin();
        return;
    }
    const isInApp = await App.jsApi.isInApp();
    // jsApi,
    if (isInApp) {
        if (config) {
            return loginUtil.goToLogin(url, param, config);
        } else {
            loginUtil.goToLogin(url, param, config);
        }
    } else {
        window.$G.goToNext("#Login/Register/CustInfoCheck");
    }
}

// 
function checkUserInfo(userMessage = {}) {
    App.requester
        .request("usr/check/lgncstinfoshowtipchk.biz", {
            slient: true, // loading
            useDefaultError: false
        })
        .then(rsp => {
            _updateAvatar(userMessage, rsp.certEndFlg, () => {
                if (rsp.LIST && rsp.LIST.length) window.$G.goToNext("#Mine/Setting/MatterList", rsp.LIST);
            });
        })
        .catch(() => _updateAvatar(userMessage, ""));
}
function _updateAvatar(rsp, certEndFlg, callback) {
    //
    if (rsp.databasePath) {
        downloadPic(rsp.databasePath, false)
            .then(res => {
                const info = { avatar: "file://" + res };
                info.certEndFlg = certEndFlg;
                updateUserInfo(info, callback);
            })
            .catch(() => {
                updateUserInfo({ certEndFlg }, callback);
            });
    } else {
        //
        updateUserInfo({ certEndFlg }, callback);
    }
}

/**
 * 
 */
function dealLoginReturn(url, param, config) {
    const { sessionout, type } = config;
    const { moduleName } = App.router.fragment;
    let timeoutReturnRouters = {
        // 
        Transfer: "#Transfer/Transfer/Index", //
        Account: "#Account/MyAccount/MyAcctList", //
        Fund: "#Fund/Index/Index", // 
        Wealth: "#FloorHome/DefaultIndex/Index", //
        //
        Deposit: "#FloorHome/DefaultIndex/Index", //
        Finance: "#Finance/Index/Index", //
        FinanceHS: "#FinanceHS/Index/Index", //
        FinanceQL: "#FinanceQL/Index/Index", //
        ChildCard: "#ChildCard/Index/Index", //
        YuEBao: "#FloorHome/Financial/Index", //
        CDTS: "#CDTS/Index/Index", //
        Loan: "#FloorHome/DefaultIndex/Index" //
    };
    const timeoutReturnRouter = timeoutReturnRouters[moduleName];

    // appapp
    // appapp
    if (sessionout && timeoutReturnRouter) return window.$G.goToNext(timeoutReturnRouter);

    const nextUrl = url.includes("#") ? url : `#${url}`;
    if (type == 2) {
        window.$G.goToNext(nextUrl, param);
        return;
    }
    if (type == 3) {
        App.menu.go(url, param);
        return;
    }
    if (type == 25) {
        return App.jsApi.openWebPageByJSBContainer({ url }); //
    }
    window.$G.goToNext(nextUrl);
}

/**
 * truetruefalse
 */
async function dealLogin() {
    const isLoginFlg = await isLogin();
    if (!isLoginFlg) {
        //
        await goToLogin("", {}, { type: 1, selfRefreshFlag: true });
        //truefalse
        return await isLogin();
    } else {
        return true;
    }
}

/**
 * app
 */
function onPause() {
    App.local.set("m_pause_time", new Date());
}

function getAppWaitTime() {
    return App.local?.get("m_app_time") ?? {};
}
/**
 * app
 */
function onResume() {
    isLogin().then(res => {
        if (res) {
            App.local.get("m_pause_time").then(rsp => {
                //
                getAppWaitTime().then(AppWtime => {
                    let tpause = 0;
                    if (AppWtime) {
                        //
                        tpause = AppWtime * 60 * 1000;
                    } else {
                        tpause = 5 * 60 * 1000;
                    }
                    //APP
                    let mNowTime = new Date();
                    let mNowTimeMs = mNowTime.getTime();
                    //APP
                    let mPauseTimeMs = new Date(rsp);
                    mPauseTimeMs = mPauseTimeMs.getTime();
                    //
                    let interval = mNowTimeMs - mPauseTimeMs;
                    if (interval > tpause) {
                        // 
                        // 
                        App.jsApi.updateLoginInfo();
                        window.$G.goToNext("#FloorHome/DefaultIndex/Index");
                    }
                });
            });
        }
    });
}
/**
 * 
 */
async function clearLoginFlag() {
    await App.requester
        .request("usr/logout/userlogout.biz", { resolution: true })
        .finally(() => {
            App.jsApi.updateLoginInfo();
        })
        .catch(() => { });
}

/**
 * 
 */
function loginByWX() {
    return loginUtil.loginByWX();
}

/**
 * 
 * @param {*} phoneNo 
 */
function bindThirdPlat(phoneNo) {
    return loginUtil.bindThirdPlat(phoneNo);
}
/**
 * 
 */
function gotoExit() {
    return new Promise(resolve => {
        App.getViewInstance("content").$confirm({
            title: "",
            message: "",
            sureText: "",
            cancelText: "",
            closed: (isSure) => {
                //   ""
                if (isSure) {
                    // 
                    clearLoginFlag().then(() => {
                        ViewBase.prototype.$toast("");
                        resolve(true);
                    })
                } else {
                    resolve(false);
                }
            }
        });

    })
}
export {
    goToLogin,
    dealLogin,
    onResume,
    onPause,
    clearLoginFlag,
    loginByWX,
    bindThirdPlat,
    dealLoginReturn,
    getAppWaitTime,
    gotoExit
};
