/**
 *  
 */
export function specialStrReplace(jsonData) {
    try {
        jsonData = JSON.parse(jsonData);
        for (let key in jsonData) {
            if (jsonData[key].includes("&apos;")) {
                jsonData[key] = jsonData[key].replace(/\&apos;/g, "'");
            }
        }
    } catch (e) {
        // do nothing
    }
    return jsonData || {};
}
/**
/**
 * 
 */
export async function transformPushMessage(result) {
    // 
    const { msgTitle, msgText, msgType, busData } = result;
    let param = {
        title: msgTitle || "",
        content: msgText || "",
        payload: result
    };
    // 
    const jsonData = specialStrReplace(busData);
    // 
    if (["M02", "M05"].includes(jsonData.msgTyp)) {
        // +
        param.title = result.msgText || "";
        param.content = jsonData.msgCntnt || "";
    }
    //
    if (msgType === "03" || msgType === "04") {
        param.title = jsonData.mesgTitle;
    }
    // 
    if (jsonData.summary) {
        param.content = jsonData.summary;
    }
    // 
    param.content = filtersUtils.maskText(param.content, 80); // //
    // 
    App.storage.remove("msgNoReadingNum");
    App.jsApi.pushLocalNotification(param);
    //
    _readyMsgNumFun();
}
/**
 *
 * @param {*} item 
 * @param {*} response 
 * @param {*} clientTastIdList 
 */
async function addNewMessage(item, response, clientTastIdList) {
    if (JSON.stringify(response) == "[]") {
        //
        await _insertAMessage(item);
    } else {
        //
        if (!clientTastIdList.includes(item.taskId)) {
            //
            await _insertAMessage(item);
        } else {
            // 
            if (item.taskId) {
                //taskId
                await _updateBaseMessage(item.taskId, item.txEndTm);
            }
        }
    }
}

/**
 * 
 * @param {*} slientFlag  loading
 */
export async function pullMessage(slientFlag) {
    const UserInfo = (await getLocalUserInfo()) || "";
    const loginUserNm = UserInfo.mobileNo || "";
    const branchId = UserInfo.branchNo || "";
    // 
    const updateTime = (await App.local.get("m_msg_update_time")) || "";
    //
    App.local.remove("last_time");
    App.local.set("last_time", updateTime);
    //apm
    const isFirstStart = await App.jsApi.getNativeInfo().catch(error => { });
    if (isFirstStart.result?.nativeInfo?.clientInfo?.isfirst == "N" && !updateTime) {
    }
    const rspData = await App.requester.request("msg/msg/msghmpg.biz", {
        data: {
            loginUserNm: loginUserNm,
            branchId: branchId,
            txStrtTm: updateTime.delTime || ""
        },
        // ()
        cancelWhenRouting: false,
        slient: slientFlag || false //
    });
    //, - 
    // let nowDate = rspData.sysDate + rspData.oprTm;
    App.local.remove("m_msg_update_time");
    App.local.set("m_msg_update_time", { delTime: rspData.tmpSysTm });
    // --
    if (rspData?.msgHmpgQryNormalEntitys && rspData?.msgHmpgQryNormalEntitys.length > 0) {
        let response = await _readLocalMessage();
        const clientTastIdList = [];
        response.forEach(item => {
            //
            clientTastIdList.push(item.taskId);
        });
        await Promise.all(
            //
            rspData?.msgHmpgQryNormalEntitys.map(item => {
                return addNewMessage(item, response, clientTastIdList);
            })
        );
    }
    //
    if (rspData?.ancmIds) {
        for (let i = 0; i < rspData.ancmIds.length; i++) {
            let ancmId = rspData?.ancmIds[i]?.ancmId;
            await delAMessageByTaskId(ancmId);
        }
    }
    //--
    if (rspData?.ancmIdEntitys && rspData.ancmIdEntitys.length > 0) {
        //1.
        await _resetTopMessage;
        //2.
        for (let i = 0; i < rspData.ancmIdEntitys.length; i++) {
            let ancmId = rspData?.ancmIdEntitys[i]?.ancmId;
            await _setTopMessage(ancmId);
        }
    }
    //
    await _deleteOutDateData();
    //
    let sourceMessageList = await _readLocalMessage();
    const taskList = [
        "2022030107000773",
        "2022030107000739",
        "2022030107000639",
        "2022030107000596",
        "2022030107000555",
        "2022030107000511",
        "2022030107000460",
        "2022030107000416",
        "2022030107000265",
        "2022030107000253",
        "2022030107000249",
        "2022030107000238",
        "2022030107000221",
        "2022030107000215",
        "2022030107001014",
        "2022030107000890",
        "2022030107000848"
    ];
    for (let i = 0; i < sourceMessageList.length; i++) {
        let taskId = sourceMessageList[i]?.taskId;
        if (taskList.includes(taskId)) {
            //taskId
            await delAMessageByTaskId(taskId);
        }
    }
}

/**
 * push
 * @param {*} msgData
 */
export async function messageJump(msgData) {
    // 
    if (
        msgData.msgType == "01" ||
        msgData.msgType == "03" ||
        msgData.msgType == "04" ||
        msgData.msgType == "05" ||
        msgData.msgType == "06" ||
        msgData.msgType == "07" ||
        msgData.msgType == "10" ||
        msgData.msgType == "12" ||
        msgData.msgType == "13"
    ) {
        App.DC.onEvent("CMMMON_ADVISORY_INFO", {
            CMM_ADVINF_TYPE: `${msgData.msgType}`
        });
    }
    // 
    if (msgData.msgBizTyp == "03") {
        App.DC.onEvent("COMMON_MSG_ANCM", {
            MSG_ANCM_NO: msgData.taskId,
            MSG_ANCM_TTL: msgData.mesgTitle,
            MSG_ANCM_RANGE_TYP: msgData.msgBizTyp,
            MSG_ANCM_STCK_FLG: msgData.strusFlg
        });
    }

    App.storage.remove("msgNoReadingNum");
    // json
    let jsonData = specialStrReplace(msgData?.busData);
    // 
    if (msgData.msgType === "V1") {
        // 02,
        if (jsonData.msgBizTyp) {
            return App.jsApi.openWebPageByWebContainer({
                url: "index.html#MessageCenter/MessageCenter/ListAccount"
            });
        } else {
            // V1-- urljson
            let fugUrl = jsonData.url; // 
            if (jsonData.param) {
                fugUrl = jsonData.url + jsonData.param;
            }
            return App.jsApi.openWebPageByJSBContainer({
                url: fugUrl,
                title: jsonData.title
            });
        }
    } else if (jsonData.msgTyp === "B00") {
        // B00  url
        if (jsonData.msgUrl) {
            //
            jsonData.msgUrl = decodeURIComponent(jsonData.msgUrl);
            return App.jsApi.openWebPageByWebContainer({ url: jsonData.msgUrl });
        }
    } else if (jsonData.msgTyp === "M13" && jsonData.url) {
        jsonData.url = jsonData.url.replace(/\amp;/g, "");
        // 
        App.jsApi.openWebPageByWebContainer({ url: jsonData.url });
        // push
    } else if (jsonData?.msgTyp === "M12" && msgData?.msgType == "01") {
        jumpHotDetail(jsonData, "01");
    } else if (msgData?.msgType == "08") {
        jumpHotDetail(jsonData, "08");
    } else if (jsonData.msgTyp == "M11") {
        // 
        window.$G.goToNext("#FundGather/SignManagement/Index");
    } else if (jsonData.msgTyp == "M08") {
        // 
        window.$G.goToNextRetain({
            router: "#AffectionMoment/AffectionSafetyBell/ProtecteeRecord"
        });
    } else if (jsonData.msgTyp == "M09") {
        // -
        window.$G.goToNextRetain({
            router: "#AffectionMoment/AffectionSafetyBell/ProtectorRecord",
            param: "100002"
        });
        //  812
    } else if (jsonData.msgTyp == "M01" || jsonData.msgTyp == "M50") {
        // 808808
        window.$G.goToNextRetain({ router: "#Mine/Setting/IdentityInfo" });
        // 
    } else if (jsonData.msgTyp == "M02") {
        window.$G.goToNextRetain({
            router: "#Statement/Monthly/Cover",
            param: { date: jsonData.txMo }
        });
    } else if (jsonData.msgTyp == "M30") {
        //
        window.$G.goToNextRetain({
            router: "#Assistant/Assistant/ElectronPayrollQuery",
            param: { acctNo: jsonData.acctNo, type: "message" }
        });
    } else if (jsonData.msgTyp == "M31" || jsonData.msgTyp == "M32") {
        //  
        window.App.menu.go("MBDEP070100");
    } else if (jsonData.msgTyp == "M33" || jsonData.msgTyp == "M37" || jsonData.msgTyp == "054001") {
        // 
        window.App.menu.go(App.bank.flags.finance?.FinanceMyIndex);
    } else if (jsonData.msgTyp == "M34") {
        // 
        window.$G.goToNext(`#${App.bank.flags.finance.module}/My/Index`);
        // 
    } else if (jsonData.msgTyp == "M35" || jsonData.msgTyp == "M36") {
        //
        window.App.menu.go("MBOLDSFUD020100", "SFUD020100.html#/productMy/index");
    } else if (jsonData.msgTyp == "M14") {
        // 
        window.$G.goToNextRetain({
            router: "#FundGather/SignManagement/Index",
            param: { tabIndex: "1002" }
        });
    } else if (jsonData.msgTyp == "M15") {
        // 
        window.$G.goToNextRetain({
            router: "#FundGather/SignManagement/Index",
            param: { tabIndex: "1001" }
        });
    } else if (jsonData.msgTypId == "052701" && jsonData.url) {
        // 
        data.url = decodeURIComponent(jsonData.url);
        return App.jsApi.openWebPageByWebContainer({ url: jsonData.url }); //6.0
    } else if (jsonData?.msgTyp == "052801" && jsonData?.pdNo) {
        // 
        window.$G.goToNextRetain({
            router: "#nobleMetal/Index/ProductDetail",
            param: { chnlPdNo: jsonData.pdNo }
        });
    } else if (jsonData?.msgTyp == "M51" && jsonData?.taskId) {
        // 
        window.$G.goToNextRetain({
            router: "#ProductExperience/Questionnaire/Questionnaire",
            param: { qstnrNo: jsonData.taskId }
        });
    } else if (jsonData?.msgTyp == "052802" && jsonData?.pgNo && jsonData?.pdCd) {
        // 810818 pgNo:  pdCd:id
        FinanceUtil.goProductDetail(jsonData);
    } else if (jsonData?.msgTyp == "006302" || jsonData?.msgTyp == "055701") {
        // 816
        window.App.menu.go("MBFIN080200");
    } else if (data?.msgTyp == "001801") {
        // 810
        window.$G.goToNextRetain({
            router: "#ElecHealthInsCard/Progress/Detail",
            param: { id: data?.orderNo }
        });
    } else {
        let codeObj = {
            // push
            "01": "index.html#MessageCenter/MessageCenter/ListNotice", //
            "02": "index.html#MessageCenter/MessageCenter/ListAccount", //
            "03": "index.html#MessageCenter/MessageCenter/ListAnnouncement", //
            "04": "index.html#MessageCenter/MessageCenter/ListBranch", //
            "05": "index.html#MessageCenter/MessageCenter/ListLife", //
            "06": "index.html#MessageCenter/MessageCenter/ListFinance", //
            "07": "index.html#MessageCenter/MessageCenter/ListCreditCard", //
            "08": "index.html#MessageCenter/MessageCenter/ListHotspot", //
            "10": "index.html#MessageCenter/MessageCenter/ListFund", //
            "12": "index.html#MessageCenter/MessageCenter/ListEconomy", //
            "13": "index.html#MessageCenter/MessageCenter/ListBenefitFarmers", //
            "15": "index.html#MessageCenter/MessageCenter/ListProductRemend" //-817
        };

        // webView
        return App.jsApi.openWebPageByWebContainer({ url: codeObj[msgData.msgType] });
    }
}
export function jumpHotDetail(jsonData, jumpFlag = "") {
    if (jsonData?.msgTyp == "M12") {
        if (jsonData?.MSG_URL_TYPE == "1" && jsonData?.url) {
            // 
            return App.jsApi.openWebPageByWebContainer({ url: jsonData.url });
        } else if (jsonData?.MSG_URL_TYPE == "2" && jsonData?.msgUrl && jsonData?.pdCd) {
            //,chnlPdClsNo
            window.App.menu.go(jsonData.msgUrl, jsonData);
        } else if (jsonData?.MSG_URL_TYPE == "3" && jsonData?.msgUrl) {
            //
            return App.jsApi.openWebPageByWebContainer({ url: jsonData.msgUrl });
        } else if (jsonData?.MSG_URL_TYPE == "4" && jsonData?.msgUrl) {
            //
            if (jsonData?.strategyId && jsonData?.msgUrl == "MBOLD1V7000") {
                //
                let url = jsonData.msgUrl.substr(5, 10);
                return App.jsApi.openWebPageByCordovaContainer({
                    menuId: url,
                    page: `${url}.html?strategyId=${jsonData.strategyId}`
                });
            } else {
                //
                return window.App.menu.go(jsonData.msgUrl);
            }
        } else if (jumpFlag == "08") {
            //
            return App.jsApi.openWebPageByWebContainer({ url: "index.html#MessageCenter/MessageCenter/ListHotspot" });
        } else if (jumpFlag == "01") {
            //
            return App.jsApi.openWebPageByWebContainer({ url: "index.html#MessageCenter/MessageCenter/ListNotice" });
        } else {
            return;
        }
    } else {
        // 
        if (jsonData.url) {
            jsonData.url = decodeURIComponent(jsonData.url);
            return App.jsApi.openWebPageByWebContainer({ url: jsonData.url });
        } else {
            return;
        }
    }
}
/**
 * ,
 */
function _readLocalMessage() {
    let data =
        "msgBizTyp='01' or msgBizTyp='02' or msgBizTyp='03' or msgBizTyp='04' or msgBizTyp='05' or msgBizTyp='06' or msgBizTyp='07' or msgBizTyp='08' or msgBizTyp='09' or msgBizTyp='10'";
    let sqlStr = `select taskId from MSG_PUSH_TABLE where ${data} ` + ` and bkId='${App.bank.flags.common.bankId}'`;
    return App.jsApi
        .executeQuerySql({
            dbName: "MSG_PUSH_TABLE",
            sql: sqlStr
        })
        .then(res => {
            return res.result.executeResult;
        })
        .catch(() => {
            return [];
        });
}

/**
 * 
 * @param {*} data 
 */
function _insertAMessage(data) {
    // TODO 
    // 
    return App.jsApi
        .executeUpdateSql({
            dbName: "MSG_PUSH_TABLE",
            //bkId-,txStrtTm,txEndTm,tagUrlURL,iconUrlURL
            //writerNm,isuInstNo  stckFlg  (YN) strusFlg  (YN)   crt_tms 
            //upd_tms  taskId-id,ebkCustNo-,msgBizTyp-,
            // mesgTitle-,msgCntnt-,sndTms-,pcsSts-(0-),busData- 
            sqls: [
                "CREATE TABLE IF NOT EXISTS MSG_PUSH_TABLE(bkId chart(3),txStrtTm chart(20),txEndTm chart(20)," +
                    // eslint-disable-next-line max-len
                    "iconUrl chart(400),writerNm chart(60),isuInstNo chart(9),stckFlg chart(1),strusFlg chart(1), " +
                    // eslint-disable-next-line max-len
                    "taskId chart(16),ebkCustNo chart(22),tagUrl chart(400),msgBizTyp chart(4),mesgTitle chart(128)," +
                    "msgCntnt chart(8000),sndTms chart(20),pcsSts chart(1),busData chart(4000)," +
                    "crt_tms TimeStamp not null default (datetime('now','localtime')),upd_tms TimeStamp " +
                    "default (datetime('now','localtime')),constraint PK_MSG_PUSH_TABLE primary key (bkId,taskId))",
                `INSERT INTO MSG_PUSH_TABLE(bkId,txStrtTm,txEndTm,iconUrl,writerNm,isuInstNo,stckFlg,strusFlg,` +
                    `taskId,ebkCustNo,tagUrl,msgBizTyp,mesgTitle,msgCntnt,sndTms,pcsSts,busData) ` +
                    `VALUES('${data.bkId}','${data.txStrtTm}','${data.txEndTm}','${data.iconUrl}',` +
                    `'${data.writerNm}','${data.isuInstNo}','${data.stckFlg}','${data.strusFlg}',` +
                    `'${data.taskId}','${data.ebkCustNo}','${data.tagUrl}','${data.busnCd}',` +
                    `'${data.title}','${data.msgCntnt}','${data.sndBgnTms}','0','${data.busntyp}')`
            ]
        })
        .then(({ code, result }) => {
            if (
                result?.executeResult[0]?.message.includes("") ||
                result?.executeResult[1]?.message.includes("")
            ) {
                window.$G.logWarnWithCode(
                    "common/js/message.js insertAMessage then MSG_PUSH_TABLE INSERT error",
                    "code:" + code + "result:" + JSON.stringify(result) + "data:" + JSON.stringify(data),
                    true
                );
            }
        })
        .catch(({ code, orignMsg, cvrtMsg }) => {
            window.$G.logWarnWithCode(
                "common/js/message.js insertAMessage catch MSG_PUSH_TABLE INSERT error",
                "code:" + code + "orignMsg:" + orignMsg + "cvrtMsg:" + cvrtMsg + "data:" + JSON.stringify(data),
                true
            );
        });
}

