import { getLocalUserInfo } from "~/common/utils/user";
import { isLogin } from "~/common/utils/check";
const M_HIS_VIEWED = "lastUsedList";
const M_COUNT_HIS_VIEWED = "lastUsedListWithCount";
const TOURISTS = "tourists";
const MAX_LENGTH = 50;
class Viewed {
    constructor() {
        this.uuid = TOURISTS;
    }
    /**
     * 
     * @param {*} val 
     */
    async cacheViewedHistory(val) {
        // 
        let rsp = await this.getLocalData();
        let countRsp = await this.getLocalMenuCountData();
        // 
        rsp = await this.addInfo(rsp, val);
        countRsp = await this.addMenuCountInfo(countRsp, val);
        // 
        await App.local.set(M_HIS_VIEWED, rsp);
        await App.local.set(M_COUNT_HIS_VIEWED, countRsp);
    }

    /**
     * menu
     * @param {*} dataList
     * @param {*} val
     */
    async addMenuCountInfo(dataList, val) {
        let list = dataList[this.uuid] || [];
        let { param, menu } = val;
        let { bsnCode, bsnName, icon } = menu;
        let allMenus = (await this.requestAllMenuData()) || [];
        let listAllMenuBsncodes = [];
        allMenus.forEach(item => {
            if (item.childs) {
                item.childs.forEach(it => {
                    listAllMenuBsncodes.push(it.bsnCode);
                });
            }
        });
        // 
        let addStatus = list.some(item => {
            if (item.bsnCode === bsnCode) {
                item.count += 1;
                return true;
            }
        });
        if (!addStatus) {
            if (listAllMenuBsncodes.includes(bsnCode)) {
                list.push({
                    param,
                    bsnCode,
                    bsnName,
                    icon,
                    count: 1
                });
            }
        }
        dataList[this.uuid] = list;
        return dataList;
    }

    // 
    // 
    async requestAllMenuData(refreshFlag) {
        if (!this.allMenusData || refreshFlag) {
            let allMenus = await window.$G.getMenuTree("100000029", true);
            this.allMenusData = allMenus
                ?.filter(item => {
                    return (item.childs && item.childs.length) || item.nodeType === "02";
                })
                .map(item => {
                    if (item.nodeType === "02") {
                        item = {
                            ...item,
                            nodeName: item.bsnName,
                            childs: [item]
                        };
                    }
                    return item;
                });
        }
        return this.allMenusData;
    }
    async getAllMenuDict() {
        if (!this.allMenuDict) {
            let allMenuData = (await this.requestAllMenuData()) || [];
            let allMenuDict = {};
            allMenuData.forEach(item => {
                item.childs.forEach(child => {
                    allMenuDict[child.bsnCode] = child;
                });
            });
            this.allMenuDict = allMenuDict;
        }
        return this.allMenuDict;
    }
    /**
     * 
     * @param {*} info 
     * @param {*} val 
     */
    async addInfo(dataList, val) {
        let list = dataList[this.uuid] || [];
        let { param, menu } = val;
        let { bsnCode } = menu;
        let allMenuDict = await this.getAllMenuDict();
        if (allMenuDict[bsnCode]) {
            let { bsnName, icon } = allMenuDict[bsnCode];
            // 
            list.unshift({
                param,
                bsnCode,
                bsnName,
                icon
            });
            // 
            for (let i = 1; i < list.length; i++) {
                if (list[i].bsnCode === bsnCode) {
                    list.splice(i, 1);
                    // bsnCode
                    break;
                }
            }
            dataList[this.uuid] = list.slice(0, MAX_LENGTH);
        }
        return dataList;
    }

    /**
     * 
     */
    async updateUuid() {
        let uuid = TOURISTS;
        let state = await isLogin();
        if (state) {
            let userInfo = await getLocalUserInfo();
            uuid = userInfo && userInfo.cstNo;
        }
        this.uuid = uuid;
        return uuid;
    }

    /**
     * 
     * @param {*} val
     */
    async removeHistory(bsnCode) {
        let rsp = await this.getLocalData();
        let list = rsp[this.uuid] || [];
        for (let i = 0; i < list.length; i++) {
            if (list.bsnCode === bsnCode) {
                // bsnCode
                list.splice(i, 1);
                // bsnCode
                break;
            }
        }
        rsp[this.uuid] = list;
        await App.local.set(M_HIS_VIEWED, rsp);
        return rsp;
    }

    /**
     * 
     * @param {*} key key
     */
    async clearHistory() {
        let rsp = await this.getLocalData();
        // 
        rsp[this.uuid] = [];

        await App.local.set(M_HIS_VIEWED, rsp);
        return rsp;
    }

    /**
     * 
     */
    async getLocalData() {
        await this.updateUuid();
        let rsp = await App.local.get(M_HIS_VIEWED);
        rsp = rsp || {};
        if (this.uuid !== TOURISTS) {
            // 
            delete rsp[TOURISTS];
        }
        return rsp;
    }
    /**
     * 
     */
    async getLocalMenuCountData() {
        await this.updateUuid();
        let rspData = await App.local.get(M_COUNT_HIS_VIEWED);
        rspData = rspData || {};
        return rspData;
    }
    /**
     * 
     */
    async sortCountHisData() {
        let countDataList = await this.getLocalMenuCountData();
        countDataList = countDataList[this.uuid] || [];
        return countDataList.sort(function(a, b) {
            return b.count - a.count;
        });
    }

    /**
     * 
     */
    async getHistory() {
        let rsp = await this.getLocalData();
        return rsp[this.uuid] || [];
    }
    /**
     * 
     * length  
     */
    async getCountHistory(length) {
        let sortData = await this.sortCountHisData();
        if (sortData.length) {
            return sortData.slice(0, length);
        } else {
            return [];
        }
    }
}

const viewed = new Viewed();
export { viewed };
