/** * 6.0wap * 806 */import { ViewBase } from "@ehas/core";import { getUrlParam } from "~/common/utils/https";import { initWXRouter, getRouterParam } from "~/common/js/wapWeChatRouter";class WapWeChatUtil {    /**     * -     * 806     * (urlqueryfrom=WeChat)     * @return {} Boolean     */    static isWXBusiness() {
//EHAS_RESULT_BRANCH_START false 

//EHAS_RESULT_BRANCH_END 
        return false;    }    /**     *      * @return {} Boolean      */    static async isWXLogin() {        //        const isLogin = App.storage.get("WXLoginFlag");        if (isLogin !== undefined) return isLogin;        //url        const token = getUrlParam(location.href, "token");        const openAcc = getUrlParam(location.href, "openAcc");        //token        if (!token) {            App.storage.remove("WXLoginFlag");            App.storage.set("WXLoginFlag", false);            return false;        }        //        return App.requester            .request("usr/cust/wxusrinfoqry.biz", {                data: {                    wxPlcId: openAcc,                    token: token                }            })            .then(rsp => {                //requestForWapSetting sid                if (rsp.bindStatus == "1") {                    //                    App.storage.remove("WXLoginFlag");                    App.storage.set("WXLoginFlag", true);                    //wechatOpenid  loginUserNm                    App.storage.remove("wechatOpenid");                    App.storage.set("wechatOpenid", rsp.loginUserNm);                    return true;                } else {                    //                    ViewBase.prototype.$confirm({                        message: "",                        sureText: "",                        cancelText: "",                        closed() {                            WapWeChatUtil.closeWebPage();                        }                    });                    return false;                }            });    }    /**     * token     */    static async dealWXLogin() {        if (await WapWeChatUtil.isWXLogin()) {            return true;        } else {            ViewBase.prototype.$alert("");            return false;        }    }    /**     *      */    static closeWebPage() {        window.close();    }    /**     * routerBefore     * @param {*} routerUrl      */    static async routerBefore(routerUrl) {        routerUrl = routerUrl.replace(/^#?/, "#");        //#Schema/Index/Index        if (routerUrl == "#Schema/Index/Index") {            App.router.go("#Fund/Index/Index", { igSelf: true });            return false;        }        return true;    }    /**     *      */    static async routerAfter() {        //[data-module-fund]wap-wx-business        setTimeout(() => {            document.body.querySelector("[data-module-fund]")?.setAttribute?.("wap-wx-business", "");        });    }    /**     * wap     */    static initWXEnvironment() {        //         const originSetArea = App.setArea;        App.setArea = function(name, option, ...args) {            if (name == "navBar") {                document.title = option?.data?.title || `${App.bank.flags.common?.bankName}`;            } else {                // ()                originSetArea.call(App, name, option, ...args);            }        };        //        initWXRouter();        //        setTimeout(() => {            WapWeChatUtil.onBridgeReady();        }, 0);    }    /**     *      */    static onBridgeReady() {        if (typeof WeixinJSBridge == "undefined") return;        /* eslint-disable */        WeixinJSBridge.call("hideOptionMenu");        WeixinJSBridge.call("hideToolbar");        /* eslint-enable */    }    /**     *      */    static getRouterParam() {        return getRouterParam();    }}/** *  */const wapWeChatSetting = {    afterStart: function() {        if (WapWeChatUtil.isWXBusiness()) {            WapWeChatUtil.initWXEnvironment();        }    }};export { WapWeChatUtil, wapWeChatSetting };