/* eslint-disable */
function dealList(tmpList, acctList, abledFlag) {
    for (let item of tmpList) {
        let unionpay = showUnionPayIcon(item.acctNoTyp) ? "()" : "";
        item.acctNoType = item.acctNoTyp; //acctNoTyp
        //
        let _acctNoTyp = item.acctNoTyp;
        if (item.acctNoTyp == "01") {
            let acctLvlCd = "";
            if (item.acctLvlCd) {
                acctLvlCd = App.dict.get("f-acctAllLvlCd", item.acctLvlCd);
            }
            //
            item.acctNoTyp = unionpay + acctLvlCd;
        } else {
            item.acctNoTyp = unionpay + App.dict.get("f-accountTypShow", item.acctNoTyp);
        }
        let obj = {
            _acctNoTyp: _acctNoTyp,
            card: item.bkShortNm,
            cardNum: item.acctNo,
            usable: true,
            type: item.bkShortNm + item.acctNoTyp,
            logo: item.iconUrl,
            bankNo: item.cnapsBkNo,
            acctStsDtl: item.acctStsDtl, //
            acctLvlCd: item.acctLvlCd, //
            dfltAcctFlg: item.dfltAcctFlg,
            //2.0
            value: fmtSensitiveData(item.acctNo), //
            key: item.acctNo, //
            tip: item.bkShortNm + item.acctNoTyp, //
            unionBankNo: item.cnapsBkNo, //
            //
            saOpenBusn: item.saOpenBusn, //
            ...item
        };
        if (item.acctAuthFlg === "Y" && item.acctSts === "0") {
            //
            obj.usable = true; //1.0
            obj.disabled = false; //2.0
            obj.type = item.bkShortNm + item.acctNoTyp;
            obj.tip = item.bkShortNm + item.acctNoTyp;
        } else {
            if (abledFlag) {
                obj.usable = true; //1.0
                obj.disabled = false; //2.0
            } else {
                //
                obj.usable = false; //1.0
                obj.disabled = true; //2.0
            }
            obj.type = item.bkShortNm + " " + "";
            obj.tip = item.bkShortNm + " " + "";
        }
        acctList.push(obj);
    }
}
/* eslint-enable */

/**
 * : 
 *  
 * @param {*} txCd : TRN000100 ,TRN000200 ,TRN000300 ,TRN000400 
 * * @param {*} acctAuthFltFlg ,Y
 * @returns {*} obj
 * @param {*}abledFlag 
 */
function getPayAcctList(txCd, acctAuthFltFlg = "N", abledFlag) {
    let result = {};
    let acctList = [];
    return App.requester
        .request("act/acct/acctlistqry.biz", {
            data: {
                txCd: txCd,
                acctAuthFltFlg: acctAuthFltFlg
            }
        })
        .then(rsp => {
            const tmpList = rsp.LIST;
            dealList(tmpList, acctList, abledFlag);
            result = {
                chooseCard: [true],
                chooseFlag: false,
                payerAccountList: acctList,
                userName: rsp.userNm,
                //
                userLvl: rsp.userLvl
            };
            return result;
        });
}

/**
 * 
 * @returns {*} txCd 
 */
function getTkDnList(txCd) {
    return App.requester
        .request("act/acct/acctlistqry4cantkgdwn.biz", {
            data: {
                txCd: txCd
            }
        })
        .then(rsp => {
            const noAccountList = rsp.LIST;
            for (let item of noAccountList) {
                // ui 1.0
                item.cardNum = getSensitiveData(item.acctNo);

                item.usable = true;
                item.logo = item.iconUrl;
                // ui2.0
                item.disabled = false;
                item.unionBankNo = item.acctNo;
                if (item.acctLvlCd == 1) {
                    item.acctLvlCd = "";
                } else if (item.acctLvlCd == 2) {
                    item.acctLvlCd = "";
                } else if (item.acctLvlCd == 3) {
                    item.acctLvlCd = "";
                }
                item.type = item.bkShortNm + item.acctLvlCd;
            }
            return noAccountList;
        });
}

/**
 * 
 * !@6223*****2027|4F74726E6751567765424765645249346D65434849736D7937656F4D6C486F6F
 * 
 * [0:"!@6223*****2027|4F74726E6751567765424765645249346D65434849736D7937656F4D6C486F6F"
 * 1:"6223*****2027"
 * 2:"4F74726E6751567765424765645249346D65434849736D7937656F4D6C486F6F"]
 *
 * 
 * !@622312*****2027|4F74726E6751567765424765645249346D65434849736D7937656F4D6C486F6F
 * 
 * [0:"!@622312*****2027|4F74726E6751567765424765645249346D65434849736D7937656F4D6C486F6F"
 * 1:"622312*****2027"
 * 2:"4F74726E6751567765424765645249346D65434849736D7937656F4D6C486F6F"]
 * */
function getSensitiveData(value, cryptograph) {
    let matches = {
        pattern: /^!@([^|]+)\|([^|]+)/
    };
    //match 
    if (typeof value === "number") {
        value = value.toString();
    }
    //
    let vals = value ? value.match(matches.pattern) : "";
    //
    if (cryptograph) {
        return vals !== null && vals[2] ? vals[2] : value;
    } else {
        return vals !== null && vals[1] ? vals[1] : value;
    }
}

/**
 *
 *  * 
 *  1.1 acctNoTyp 
 *   01 
 *   02 
 *   03 
 *   04 
 *   05 
 *   06 
 *   07 
 *   08 /
 *   09 
 *   10 
 *   11 
 *   12 
 *   CX 
 *  1.2 acctLvlCd    
 *      1 I
 *      2 II
 *      3 III
 *  1.3 crEacctFlg  0 1
 *  1.4 acctOpenChnlNo 01 01
 *  1.5 spcCrdTypCd  01: 02: 03: 20: 21: 30:
 *  2
 *   1.11.21.3
 *  
 *  
 *     acctLvlCd  
 *   04 05  
 *  spcCrdTypCd
 *
 *  3 
 *  3.1  1.11.21.3 1.4
 *   mCardType
 *  "depositCard"    :  
 *  "eAccountCounter":II/III 
 *  "eAccountNet"    :II/III 
 *  "creditCart"     :         
 *  "otherCard"      :           
 *
 *  3.2    mCardTypeName
 *  defaultCardFlag  
 */
/* eslint-disable */
function cardTypeClassification(item) {
    // 
    let level = App.dict.get("f-acctLvlCd", item.acctLvlCd);
    level = !level ? "" : `${level}`;
    switch (item.acctNoTyp) {
        case "01":
            item.defaultCardFlag = true;

            // 21
            if (item.spcCrdTypCd == "21") {
                item.mCardTypeName = "()";
                //true
                item.cancelDebitCardShow = true;
            } else {
                item.mCardTypeName = "()" + level;
            }
            if (item.crEacctFlg == "0") {
                //
                if (item.spcCrdTypCd == "30") {
                    //true
                    item.cancelDebitCardShow = true;
                    //
                    App.bank.flags.account.showAcctNoCheckFlag && (item.showAcctNoCheck = true);
                    item.mCardTypeName = "()";
                    item.mCardType = "medicalInsurance";
                } else {
                    if (item.acctLvlCd == "1") {
                        item.mCardType = "depositCardLvl";
                    } else {
                        item.mCardType = "depositCard";
                    }
                }
            } else if (item.crEacctFlg == "1") {
                //0-I/II/III
                //1-II/III
                //mCardTypeNameDiff == "1"   815  
                if (item.acctLvlCd == "1" && App.bank.flags.account?.mCardTypeNameDiff == "1") {
                    item.mCardTypeName = "()" + level;
                } else {
                    item.mCardTypeName = "()" + level;
                }
                // if (item.acctOpenChnlNo == "01") {
                //     item.mCardType = "eAccountCounter";
                // } else {
                // ;
                let openChnlNo = App.bank.flags.account?.thirdAcctList
                    ? [...App.bank.flags.account?.thirdAcctList]
                    : [];
                //74     
                openChnlNo.push(...["03", "49", "74"]);
                if (openChnlNo.indexOf(item.acctOpenChnlNo) < 0) {
                    //   
                    if (item.acctLvlCd == "1") {
                        // I
                        item.mCardType = "depositCardLvl";
                    } else {
                        //
                        if (item.acctLvlCd == "3") {
                            item.counterSignFlag = true;
                        }
                        item.mCardType = "depositCard";
                    }
                } else {
                    // 
                    item.mCardType = "eAccountNet";
                }
                // }
            }
            break;
        case "02":
            // item.mCardTypeName =item.spcCrdTypCd == undefined ? "" : item.spcCrdTypCd == "01"?
            let mCardTypeNameFlag = App.bank.flags.account.mCardTypeNameFlag;
            switch (item.spcCrdTypCd) {
                case undefined:
                    item.mCardTypeName = "";
                    break;
                case "01":
                    if (mCardTypeNameFlag) {
                        item.mCardTypeName = "()";
                    } else {
                        item.mCardTypeName = "()";
                    }
                    App.bank.flags.account.creditCardFlag && (item.defaultCardFlag = true);
                    break;
                case "02":
                    // eslint-disable-next-line no-case-declarations
                    App.bank.flags.account.creditCardFlag && (item.defaultCardFlag = true);
                    if (mCardTypeNameFlag) {
                        item.mCardTypeName = "()";
                    } else {
                        item.mCardTypeName = "()";
                    }
                    break;
                default:
                    item.mCardTypeName = "";
                    break;
            }
            item.mCardType = "creditCart";
            break;
        case "03":
            item.mCardTypeName = "()" + (item.bkShortNm || "");
            item.mCardType = "otherCard";
            break;
        case "04":
            item.defaultCardFlag = true;
            item.mCardTypeName = "" + level;
            item.mCardType = "depositCard";
            break;
        case "05":
            item.defaultCardFlag = true;
            item.mCardTypeName = "" + level;
            item.mCardType = "depositCard";
            break;
        case "06":
            item.defaultCardFlag = true;
            item.mCardTypeName = "";
            item.mCardType = "depositReceiptCard";
            //cardClickFlag  true 
            item.cardClickFlag = App.bank.flags.account?.cardClickFlag ? true : false;
            break;
        case "07":
            item.defaultCardFlag = true;
            item.mCardTypeName = "";
            if (item.elcTdepFlg && item.elcTdepFlg == "01") {
                item.mCardTypeName = "";
            }
            //  
            item.mCardType = App.bank.flags.account?.cardClickFlag ? "depositReceiptCard" : "";
            item.cardClickFlag = App.bank.flags.account?.cardClickFlag ? true : false;
            break;
        case "08":
            item.defaultCardFlag = true;
            item.mCardTypeName = "";
            item.mCardType = "depositReceiptCard";
            item.cardClickFlag = App.bank.flags.account?.cardClickFlag ? true : false;
            break;
        case "11":
            item.defaultCardFlag = true;
            item.mCardTypeName = "";
            item.mCardType = "depositReceiptCard";
            item.cardClickFlag = App.bank.flags.account?.cardClickFlag ? true : false;
            break;
        case "12":
            item.defaultCardFlag = true;
            item.mCardTypeName = "";
            item.mCardType = "depositReceiptCard";
            item.cardClickFlag = App.bank.flags.account?.cardClickFlag ? true : false;
            break;
        default:
            item.mCardTypeName = "";
            item.mCardType = "";
            break;
    }
}
