function isPhoneCardFrame(clipWay, currClip) {
    const title = clipWay == "phoneNo" ? "" : "";
    const account = clipWay == "phoneNo" ? phoneUtils.format(currClip) : accountUtils.format(currClip);
    const bulletText =
        clipWay == "phoneNo"
            ? `<p class="T16 LH24 Bd" style="text-align: center;">${title}</p><p class="T16 LH24 Bd" style="text-align: center;">${account}</p>`
            : `<p class="T16 LH24 Bd" style="text-align: center;">${title}</p><p class="T16 LH24 Bd" style="text-align: center;">${account}</p>`;
    if (isNewStartup()) {
        App.jsApi
            .showTransfersAlert({
                title: title,
                message: account
            })
            .then(res => {
                if (res.result?.status == "1") {
                    _toTransfer(clipWay, currClip);
                }
            });
    } else {
        // 
        App.getViewInstance("content").$confirm({
            type: "t3",
            message: bulletText,
            sureText: "",
            cancelText: "",
            closed: isSure => {
                if (isSure) {
                    _toTransfer(clipWay, currClip);
                }
            }
        });
    }
}

function _toTransfer(clipWay, currClip) {
    if (clipWay == "phoneNo") {
        // 
        window.$G.goToNext("#Transfer/Transfer/TransferByPhoneNo", {
            payeePhone: currClip
        });
    } else {
        // 
        window.$G.goToNext("#Transfer/Transfer/TransferByCard", {
            rcvAcctNo: currClip
        });
    }
}

/**
 * luhn1619:
 * 1. 1518 1  1518 2
 * 2.
 * 3. 10 
 * @param {*} bankno 
 * @returns {*} Boolean
 */
/* eslint-disable */
function bankCardValidate(bankno) {
    // 0
    if (parseInt(bankno) == 0) {
        return false;
    }

    //luhn
    const lastNum = +bankno.substr(bankno.length - 1, 1);
    //1518
    const otherNum = bankno.substr(0, bankno.length - 1);
    //1518
    const newArr = [...otherNum].reverse();
    //*2 <9
    const arrOdd = [];
    //*2 >9
    const arrOdd2 = [];
    //
    const arrEven = [];
    for (let j = 0; j < newArr.length; j += 1) {
        if ((j + 1) % 2 === 1) {
            //
            if (parseInt(newArr[j], 10) * 2 < 9) {
                arrOdd.push(parseInt(newArr[j], 10) * 2);
            } else {
                arrOdd2.push(parseInt(newArr[j], 10) * 2);
            }
        } else {
            //
            arrEven.push(newArr[j]);
        }
    }
    //*2 >9 
    const oddChild1 = [];
    //*2 >9 
    const oddChild2 = [];
    for (let h = 0; h < arrOdd2.length; h += 1) {
        oddChild1.push(parseInt(arrOdd2[h], 10) % 10);
        oddChild2.push(parseInt(arrOdd2[h], 10) / 10);
    }
    //*2 < 9 
    let sumOdd = 0;
    //
    let sumEven = 0;
    //*2 >9 
    let sumOddChild1 = 0;
    //*2 >9 
    let sumOddChild2 = 0;
    let sumTotal = 0;
    for (let m = 0; m < arrOdd.length; m += 1) {
        sumOdd += parseInt(arrOdd[m], 10);
    }

    for (let n = 0; n < arrEven.length; n += 1) {
        sumEven += parseInt(arrEven[n], 10);
    }

    for (let p = 0; p < oddChild1.length; p += 1) {
        sumOddChild1 += parseInt(oddChild1[p], 10);
        sumOddChild2 += parseInt(oddChild2[p], 10);
    }
    //
    sumTotal = parseInt(sumOdd, 10) + parseInt(sumEven, 10) + parseInt(sumOddChild1, 10) + parseInt(sumOddChild2, 10);
    const k = parseInt(sumTotal, 10) % 10 === 0 ? 10 : parseInt(sumTotal, 10) % 10;
    //luhn
    const luhn = 10 - k;
    if (lastNum === luhn) {
        return true;
    } else {
        return false;
    }
}
/**
 * 
 *
()  
()
()
()


 
 */
function getCertIssueLand(certIssueLand) {
    let idOffer = certIssueLand || "";
    let shiPoint = -1,
        xianPoint = -1,
        matchStr = "";
    shiPoint = idOffer.indexOf("");
    xianPoint = idOffer.indexOf("");
    if (xianPoint == -1) {
        xianPoint = idOffer.indexOf("");
    }
    if (shiPoint > -1 && xianPoint > -1) {
        // 
        let lastCityPoint = idOffer.lastIndexOf("");
        if (lastCityPoint == shiPoint) {
            matchStr = idOffer.substring(Math.max(0, shiPoint + 1), xianPoint + 1);
        } else {
            //
            matchStr = idOffer.substring(Math.max(0, lastCityPoint + 1), xianPoint + 1);
        }
    } else if (shiPoint > 0 && xianPoint == -1) {
        // 
        let lastCityPoint = idOffer.lastIndexOf("");
        if (lastCityPoint == shiPoint) {
            matchStr = idOffer.substring(0, Math.max(0, shiPoint + 1));
        } else {
            //
            let lastSecCityPoint = idOffer.lastIndexOf("", idOffer.lastIndexOf("") - 1);
            matchStr = idOffer.substring(lastSecCityPoint + 1, Math.max(0, lastCityPoint + 1));
        }
    } else if (shiPoint == -1 && xianPoint > 0) {
        // 
        matchStr = idOffer.substring(0, Math.max(0, xianPoint + 1));
    } else {
        matchStr = idOffer;
    }
    return matchStr;
}
