/* eslint-disable */
export default {
    afterStart: function(App) {
        // beforeStartafterStartbeforeStart
        App.globalFlag = {};
        App.globalFlag.lastStartBizsReqUrl = ""
        // 
        App.globalFlag.busiSceneNo = "";
        // 
        App.globalFlag.lastReqUrl = "";
        // webview
        App.globalFlag.loginView = "";
        // 
        App.globalFlag.setRequestHead = true;
        // webview
        App.globalFlag.closeLoginStateDate = {};
        // 
        const defaultState = App.bank.flags.safeCenter?.defaultSwitchSetting ? false : true;
        ///
        App.globalFlag.noScreenCaptureUrl = [
            "Transfer/Transfer/TransferByCard", //
            "Transfer/Transfer/TransferByPhoneNo", //
            "Transfer/TransferBatch/TransferBatch", //
            "Transfer/TransferByReservation/AddByTemp", //
            "Transfer/TransferByReservation/AddInfoByTemp", //
            "Transfer/TransferByReservation/RapidAddInfoByTemp", //
            "AffectionMoment/AffectionBlessing/Add", //
            "AffectionMoment/AffectionBlessing/Preview", //
            "Login/Register/CustInfoCheck", // 
            "Login/Login/LgnByPswd", // 
            "Login/Login/DefaultLogin", // 
            "Login/Login/SpecLgnWay", // 
            "SafeCenter/LoginSetting/GesturePswdSetting", // 
            "Login/Login/SettingGesture" // 
        ];

        App.globalFlag.noScreenCaptureMineUrl = [
            "FloorHome/SimplifyMine/PerDetails", // 
            "SimpleHome/Mine/OldPerDetails", // 
            "Huinong/My/Index" // )
        ];
        if (App.bank.flags?.mine?.noScreenCapture) {
            App.globalFlag.noScreenCaptureUrl = App.globalFlag.noScreenCaptureUrl.concat(
                ...App.globalFlag.noScreenCaptureMineUrl
            );
        }
        App.local
            .get("m_version_info")
            .then(rsp => {
                if (rsp) {
                    if (rsp.simpFlg == "Y") {
                        App.globalFlag.simpleFlag = rsp.simpFlg;
                        App.globalFlag.huinong = "N";
                    } else if (rsp.huinong == "Y") {
                        App.globalFlag.huinong = rsp.huinong;
                        App.globalFlag.simpleFlag = "N";
                    }
                    App.globalFlag.autoChangeSimple = rsp.autoChangeSimple == "Y" ? "Y" : "N";
                } else {
                    App.globalFlag.simpleFlag = "N";
                    App.globalFlag.huinong = "N";
                    App.globalFlag.autoChangeSimple = "N";
                }
            })
            .catch(() => {
                App.globalFlag.simpleFlag = "N";
                App.globalFlag.huinong = "N";
                App.globalFlag.autoChangeSimple = "N";
            });
        //  
        App.local
            .get("m_clip_card_flag")
            .then(rsp => {
                if (rsp == undefined) {
                    App.local.set("m_clip_card_flag", defaultState);
                }
            })
            .catch(() => {
                App.local.set("m_clip_card_flag", defaultState);
            });
        //  
        App.local
            .get("m_clip_phone_flag")
            .then(rsp => {
                if (rsp == undefined) {
                    App.local.set("m_clip_phone_flag", defaultState);
                }
            })
            .catch(() => {
                App.local.set("m_clip_phone_flag", defaultState);
            });
    }
};
