import { getSensitiveData } from "~/common/utils/account";
import { phoneUtils } from "@ehas/utils";
import { moneyUtils } from "@ehas/utils";
import { accountUtils } from "@ehas/utils";
import { dateUtils } from "@ehas/utils";

/**
 * /
 * @param {*} value  !@6223*****2027|4F74726E6751567765424765645249346D65434849736D7937656F4D6C486F6F
 * @returns {*} 6223*****2027
 */
let fmtSensitiveData = function (val) {
    return getSensitiveData(val, false);
};

/**
 * 
 * @param {*} type 
 * @param {*} key key
 * @returns {*} 
 */
let fmtDict = function (type, key) {
    return App.dict.get(key, type);
};

/**
 * 
 * @param {*} val 18800001111 || !@188*****1111|4F74726E6751567765424765645249346D65434849736D7937656F4D6C486F6F
 * @param {*} type ,0
 * @returns {*} type=0:188 **** 1111,type=1:**** 1111
 */
let fmtPhoneNo = function (val, type = 0) {
    val = fmtSensitiveData(val);
    return phoneUtils.mask(val, type);
};

/**
 * 
 * @param {*} val 100000
 * @param {*} showCcy 0
 * @param {*} isCcy ,true
 * @returns {*} showCcy=0:1,000.00,showCcy=1:$1,000.00
 */
let fmtAmt = function (val, showCcy = 0, isCcy = true) {
    if (val == '--') {
        //----
        return '--';
    }
    if (isCcy) {
        return App.dict.get("f-showCcy", showCcy) + moneyUtils.format(val);
    }
    return moneyUtils.format(val);
};

/**
 * 
 * @param {*} val :6222021309002541254 ||
 * !@6223*****2027|4F74726E6751567765424765645249346D65434849736D7937656F4D6C486F6F
 * @param {*} maskType 0
 * @returns {*} maskType=0:6222 ******** 1254,maskType=1:**** 1254
 */
let fmtAcctNo = function (val, maskType = 0) {
    val = fmtSensitiveData(val);
    return accountUtils.mask(val, maskType);
};

/**
 * /
 * @param {*} val :6222021309002541254 ||
 * !@6223*****2027|4F74726E6751567765424765645249346D65434849736D7937656F4D6C486F6F
 * :18800001111 || !@188*****1111|4F74726E6751567765424765645249346D65434849736D7937656F4D6C486F6F
 * @param {*} length 4
 */
let fmtLast = function (val, length = 4) {
    val = fmtSensitiveData(val);
    return accountUtils.last(val, length);
};

/**
 * 
 * @param {*} val 20201111
 * @param {*} formatter "YYYY-MM-DD"
 * @returns {*} 
 */
let fmtDate = function (val, formatter = "YYYY-MM-DD") {
    if (val) {
        return dateUtils.format(val, "", formatter);
    }
    return "";
};

/**
 * 
 * @param {*} val 
 * @param {*} type , Hms:
 * @param {*} formatter , HH:mm:ss
 * @returns {*} 
 */
let fmtTime = function (val, type = "Hms", formatter = "HH:mm:ss") {
    return dateUtils.format(val, type, formatter);
};

/**
 * 0'-'
 * @param {*} val 
 * @returns {*}  '-'
 */
let fmtNull = function (val) {
    //val0'--'
    if (Number(val) == 0) {
        return Number(val);
    }
    return !Number(val) ? "--" : val;
};

/**
 * cdn
 * @example @@ebus1@@mobile/cdn_proxy/
 * @example https://802.ebanktest.com.cn:2802/mobile/cdn_proxy/
 * @param {*} url
 * @returns {*} 
 */
let transferUrl = function (url) {
    if (typeof url !== "string") return url;
    return url.replace(/@@ebus1@@/g, ehasProcessCDNServer);
};

/**
 * 
 * @param {*} val :65845264
 * @returns {*} 6584 5264
 */
let fmtVoiceVal = function (val) {
    if (val && val.length === 8) {
        return `${String(val).substr(0, 4)} ${String(val).substr(4, 4)}`;
    } else {
        return val;
    }
};

export {
    fmtSensitiveData,
    fmtDict,
    fmtPhoneNo,
    fmtAmt,
    fmtAcctNo,
    fmtDate,
    fmtLast,
    fmtTime,
    fmtNull,
    transferUrl,
    fmtVoiceVal
};
