import { getUrlParam } from "./https";
import { ViewBase } from "@ehas/core";
import { transferUrl } from "~/common/js/filters";
/**
 * 
 */
/* eslint-disable */
function pictureJump(data) {
    // 04: 08:   pctLinkUrl = {"pgNo":"xx",//==ID "pgParam":"xxx", // /Param1   /Param1/Param2/Param3}
    // 10 
    const { pctLinkTyp, pctLinkUrl, advgTtl, nvgFldFlg, nvgFldNm } = data;
    switch (pctLinkTyp) {
        case "01":
        case "06":
            let searchArr = pctLinkUrl.split("#");
            let appId = "";
            let isHiddenNavigationBar = "";
            let licence = "";
            for (let i = 0; i < searchArr.length; i++) {
                const _appId = getUrlParam(searchArr[i], "appId");
                const _isHiddenNavigationBar = getUrlParam(searchArr[i], "isHiddenNavigationBar");
                const _licence = getUrlParam(searchArr[i], "licence");
                if (_appId) appId = _appId;
                if (_isHiddenNavigationBar) isHiddenNavigationBar = _isHiddenNavigationBar;
                if (_licence) licence = _licence;
            }
            if (appId) {
                // 
                App.jsApi
                    .openApplet({
                        url: pctLinkUrl,
                        appId: appId,
                        licence: licence,
                        title: nvgFldNm,
                        isHiddenNavigationBar: isHiddenNavigationBar
                    })
                    .catch(rsp => {
                        //codemessge
                        if (rsp.code == "EPFJ002W") {
                            ViewBase.prototype.$alert({
                                message: "",
                                btnText: ""
                            });
                        } else {
                            ViewBase.prototype.$toast(rsp.cvrtMsg);
                        }
                    });
            } else if (nvgFldFlg == "Y") {
                //
                App.jsApi.openWebpageByInnerBrowser({
                    url: pctLinkUrl,
                    isShowNavigation: true,
                    title: nvgFldNm
                });
            } else {
                App.jsApi.openWebPageByWebContainer({ url: pctLinkUrl });
            }
            break;
        case "02":
        case "07":
            App.jsApi.openWebpageByBrowser({ url: pctLinkUrl });
            break;
        case "03":
            if (pctLinkUrl == "MBUSR010000" && data.fromCreditCard) {
                App.menu.go(pctLinkUrl, { fromCreditCard: data.fromCreditCard });
            } else {
                App.menu.go(pctLinkUrl);
            }
            break;
        case "05":
            if (nvgFldFlg == "Y") {
                //
                //nvgFldFlgYtitleN
                App.jsApi.openWebPageByJSBContainer({ url: pctLinkUrl, title: nvgFldNm });
            } else {
                // 5.0APP
                App.jsApi.openWebPageByJSBContainer({ url: pctLinkUrl, title: "" });
            }
            break;
        case "04":
        case "08":
            // JSONjson
            if (data.isMessageCenter) {
                App.menu.go(pctLinkUrl.pgNo, pctLinkUrl.pgParam);
            } else {
                App.menu.go(JSON.parse(pctLinkUrl).pgNo, JSON.parse(pctLinkUrl).pgParam);
            }
            break;
        // 
        case "09":
            window.$G.goToNext("#Video/Video/VideoDetail", data);
            break;
        case "10":
            App.menu.go(JSON.parse(pctLinkUrl).pgNo, JSON.parse(pctLinkUrl).pgParam);
            break;
        // 11 
        case "11":
            const { sourceId } = data;
            // userNameID 
            // path 
            if(sourceId) {
                App.jsApi.openWechatMiniProgram({ userName: sourceId, path: pctLinkUrl }).catch(rsp => {
                    // webview
                    // codemessge
                    if (rsp.code == "EPFN515T") {
                        ViewBase.prototype.$alert({
                            message: "",
                            btnText: ""
                        });
                    } else {
                        ViewBase.prototype.$alert(rsp.cvrtMsg);
                    }
                });
            }
            break;
        default:
            break;
    }
}
/**
 * 
 * @param {*} adType
 */
async function getAdInfo(adType) {
    let rsp = {};
    try {
        rsp = await App.jsApi.getSpecifiedAdInfoByCache({ adType });
    } catch (e) {
        window.$G.jsApiErrorMsg(e);
    }
    try {
        let adInfo = rsp.result.adInfo;
        const MenuId = await window.$G.getMenuId();
        adInfo = adInfo.filter(item => {
            item.pctUrlShow = transferUrl(item.pctUrl);
            return item?.pageId == MenuId;
        });
        return adInfo;
    } catch (e) {
        return [];
    }
}
/* eslint-enable */

export { pictureJump, getAdInfo };
