import { ViewBase } from "@ehas/core";
//
import { filtersUtils } from "@ehas/utils";
//
/* eslint-disable */

import filter from "~/common/utils/filters";
import * as custom from "~/common/js/filters";
let setMenuFontSize = function(el, binding) {
    setTimeout(() => {
        let classNames = el.getAttribute("class");
        let text = el.innerText;
        if (text) {
            let fontSize = binding.value || 16;
            let length = text.length;
            if (length > 5) {
                fontSize = 14;
            }
            el.style.width = "100%";
            el.style.overflow = "hidden";
            el.style.textOverflow = "ellipsis";
            el.style.whiteSpace = "nowrap";
            el.setAttribute("class", classNames.concat(" LH16 T" + fontSize));
        }
    });
};
let computedScrollHeight = function(el, binding) {
    setTimeout(() => {
        const headerHeight = binding.value?.headerHeight || 0; // 
        const footerHeight = binding.value?.footerHeight || 0; // 
        const { top } = el.getBoundingClientRect();
        el.style.height = `calc(100vh - ${top}px - ${headerHeight}px - ${footerHeight}px)`;
    });
};
export default {
    beforeStart: function(App) {
        //
        let filters = Object.assign({}, filter, filtersUtils, custom);
        Object.keys(filters).forEach(key => {
            ViewBase.prototype[key] = filters[key];
            ViewBase.filter(key, filters[key]);
        });
        //
        ViewBase.directive("go", {
            bind(el, binding) {
                el?.addEventListener(
                    "click",
                    () => {
                        App.router.go(binding.value);
                    },
                    false
                );
            }
        });
        //  v-back
        ViewBase.directive("back", {
            bind(el) {
                el?.addEventListener(
                    "click",
                    () => {
                        App.router.back();
                    },
                    false
                );
            }
        });
        // 
        ViewBase.directive("scrollheight", {
            bind(el, binding) {
                computedScrollHeight(el, binding);
            },
            update(el, binding) {
                computedScrollHeight(el, binding);
            }
        });
        /*
         * 
         * 516
         * 5144+...
         */

        ViewBase.directive("menuHuinong", {
            bind(el, binding) {
                setMenuFontSize(el, binding);
            },
            update(el, binding) {
                setMenuFontSize(el, binding);
            }
        });
    }
};
