import TitleConf from "../config/title.config";
import { onlineUrlFuc } from '~/common/utils/customerService';


function setHeadOption(TitleCate = "defaultTitle", defaultFunc = null, secFunc = null) {
    let titleData = TitleConf[TitleCate];
    Object.assign(titleData, { clickRightHandler: clickRightHandler });

    App.setArea("navBar", {
        // 
        data: titleData,
        methods: {
            // icon
            // iconRight() {
            //     if (defaultFunc) {
            //         // 
            //         defaultFunc && defaultFunc();
            //     } else {
            //         //  "jumpUrl" 
            //         window.$G.goToNext(TitleConf[TitleCate]["defaultUrl"]);
            //     }
            // },
            // icon
            iconRightSec() {
                if (secFunc) {
                    // 
                    secFunc && secFunc();
                } else {
                    //  "jumpUrl" 
                    window.$G.goToNext(TitleConf[TitleCate]["secUrl"]);
                }
            }
        }
    });

    // icon
    function clickRightHandler() {
        if (defaultFunc) {
            // 
            defaultFunc && defaultFunc();
        } else {
            let param = {
                pageUrl: App.router.fragmentUrl
            };
            //  "jumpUrl" 
            if (TitleConf[TitleCate]["defaultUrl"] == '#IntelligentService/CustomerService/Index') {
                //
                onlineUrlFuc({
                    pageUrl: App.router.fragmentUrl
                });
            } else {
                //
                window.$G.goToNext(TitleConf[TitleCate]["defaultUrl"], param);
            }
        }
    }
}
export { setHeadOption };
