import { getJson } from "@ehas/request-ajax";import { WapWeChatUtil } from "~/common/js/wap";/** * moduleName cdn * fileName cdn * jsoncdn */async function dealWithJsonFun(moduleName, fileName) {    try {        const bankId = App.bank.flags?.common?.bankId;        let requestPath = `${ehasProcessCDNServer}/public/${bankId}/h5/mbank/${moduleName}/json/${fileName}.json`;        //        let jsonData = await getJson(requestPath);        return jsonData.data || "";    } catch (_error) {        return "";    }}/** * url */function getUrlParam(url, name) {    let searchArr = url.split("#")[0].split("?");    if (searchArr.length > 1) {        let params = searchArr[1].split("&");        let _arr = [];        for (let i = 0; i < params.length; i++) {            _arr = params[i].split("=");            if (_arr !== null && _arr[0] === name) {                return _arr[1];            }        }        return "";    } else {        return "";    }}/** *URl * @param {string} url url * @param {string} name  *//* eslint-disable */function getUrlParams(url, name) {    let reg = new RegExp("(&|\\?)" + name + "=([^&]*)(&|#|$)");    //let urlParamStr = url.split("?")[1];    let r = url.match(reg);    if (r != null) return decodeURI(r[2]);    return null;}/* eslint-enable *//** *  * @param {*} path ,file.config.js * @param {*} picPath jsApi * @param {*} bucketType MinIO,file.config.js *//* eslint-disable */async function uploadPic(path, picPath, bucketType = "1") {    const fileData = {        // ,:transfer:/transfer/phone        upload: {            setting: "/setting/feedback",            childCard: "/index/index",            completeMemberInfo: "/affectionAccount/completeMemberInfo",            fudQlfdinvstApply: "/fud/QlfdinvstApply",            //             questionnaire: "/productExperience/questionnaire",            //            creditcardInformation:"/modules/CreditCard/Upload"        },        download: {},        bucketType: {            "1": "/customer", //            "2": "/public" //        }    };    const fileArr = picPath.split("/");    const fileName = fileArr[fileArr.length - 1];    //     const filePath = fileData["bucketType"][bucketType] + fileData["upload"][path];    // token    const tokenObj = await App.requester.request("file/upload/token.do", {        data: {            filePath: filePath,            fileName: fileName        }    });    const token = tokenObj.token;    // jsapi    const picObj = await App.jsApi.uploadFile4Biz({        fileName: fileName,        filePath: picPath,        token: token    });    let picInfo = {        picName: "",        picUrl: ""    };    // ,,    if (picObj.result.rspHead.rspCd === "S000A000") {        picInfo.picName = picObj.result.rspData.fileName;        picInfo.picUrl = picObj.result.rspData.url;        return picInfo;    } else {        window.$G.popErrorMsg(picObj.result.rspHead.rspCd, picObj.result.rspHead.msg, picObj.result.rspHead.errorId);    }}/* eslint-enable *//** *  * @param {*} picPath  * @returns {*} (file) */async function downloadPic(picPath, cancelWhenRouting = true) {    const fileArr = picPath.split("/");    const fileName = fileArr[fileArr.length - 1];    const tokenObj = await App.requester        .request("file/download/token.do", {            data: {                fileFullPath: picPath            },            useDefaultError: false,            cancelWhenRouting: cancelWhenRouting        })        .catch(err => {
//EHAS_RESULT_BRANCH_START true 
            window.$G.popErrorMsg(err);
//EHAS_RESULT_BRANCH_END 
        });    const token = tokenObj.token;    const picObj = await App.jsApi.downloadFile4Biz({ fileName: fileName, token: token }).catch(err => {
//EHAS_RESULT_BRANCH_START true 
        window.$G.popErrorMsg(err);
//EHAS_RESULT_BRANCH_END 
    });    return picObj.result.filePath;}/** *  * @param {*} type  * @param {*} bankid  */function getDomainName(type, bankid = App.bank.flags.common.bankId) {    let value = "";
//EHAS_RESULT_BRANCH_START false 

//EHAS_RESULT_BRANCH_END 

//EHAS_RESULT_BRANCH_START true 
    const name = `${ehasProcessContextFlag}.config`;    const plugin = require(`../config/domainName/${name}`);    value = plugin[bankid][type];
//EHAS_RESULT_BRANCH_END 
    return value;}/** * submitKey * @param {*} url url */async function onReqSubmitKey(url) {    if (url.substring(0, 1) === "/") {        url = url.substring(1, url.length);    }    const urlList = url.split("/");    let submitKeyObj = null;    try {        submitKeyObj = await App.requester.request(urlList[0] + "/submitkey.biz", {            useDefaultError: false,            slient: true,            resolution: true        });        const submitKey = submitKeyObj.submitKey;        return submitKey;    } catch (error) {        window.$G.logErrorWithCode("common/utils/https.js", "submitKey" + JSON.stringify(error));        //await        try {            submitKeyObj = await App.requester.request(urlList[0] + "/submitkey.biz", {                useDefaultError: false,                slient: true,                resolution: true            });            const submitKey = submitKeyObj.submitKey;            return submitKey;        } catch (err) {            window.$G.logErrorWithCode("common/utils/https.js", "submitKey" + JSON.stringify(err));            return "";        }    }}/* eslint-disable */function getHmacSecretKey() {
//EHAS_RESULT_BRANCH_START false 

//EHAS_RESULT_BRANCH_END 

//EHAS_RESULT_BRANCH_START true 
    return "d6c86549-5c8e-4be9-863d-056289d07fbd";
//EHAS_RESULT_BRANCH_END 
}function getRequestAppid() {    if (WapWeChatUtil.isWXBusiness()) {        return "0000000027c84c07ffffffff9a3e4a8e";    }
//EHAS_RESULT_BRANCH_START false 

//EHAS_RESULT_BRANCH_END 

//EHAS_RESULT_BRANCH_START true 
    return "00000000-3950-d063-ffff-ffffcedfe028";
//EHAS_RESULT_BRANCH_END 
}/* eslint-enable *//** *  * @param {*} response blob */function readResAsString(response) {    // gbkgb2312sofa 3gbk    return new Promise((resolve, reject) => {        const reader = new window.FileReader();        //         var hasMatch = false;        reader.onload = () => {            if (!hasMatch) {                //                 const charsetReg = /charset=(?<charset>(gb2312|sofa|utf-8|gbk|x-cp20936))/gi;                //                 const matchResult = charsetReg.exec(reader.result.match(charsetReg));                //                 let charset = matchResult?.groups?.charset;                //                 hasMatch = true;                //  utf-8                if (charset && charset.toUpperCase() !== "UTF-8") {                    if (charset == "x-cp20936") {                        charset = "gb2312";                    }                    reader.readAsText(response, charset);                } else {                    resolve(reader.result);                }            } else {                resolve(reader.result);            }        };        reader.onerror = reject;        // utf-8        reader.readAsText(response, "utf-8");    });}/** *  * @param {*} list  */function agrTypVerData(list) {    return list.map(item => {        let agrContent = item.content.substring(item.content.indexOf("?"), item.content.indexOf('">'));        return {            agrVerNo: getUrlParam(agrContent, "verNo"),            agrTyp: getUrlParam(agrContent, "agrTyp")        };    });}/** *  * @param {*} list  */function agrCheckRecord(list) {    if (list.length > 0) {        App.requester.request("cmm/agr/agrfilerecd.biz", {            useDefaultError: false,            data: {                agrFileRecdListDtoEntityList: list            }        });    }}export {    dealWithJsonFun,    getUrlParam,    getUrlParams,    uploadPic,    downloadPic,    getDomainName,    onReqSubmitKey,    getHmacSecretKey,    getRequestAppid,    readResAsString,    agrTypVerData,    agrCheckRecord};