import { goToLogin } from "~/common/utils/login.js";
import { isLogin as checkIsLogin } from "~/common/utils/check";
import memnu from "~/common/datas/menu.js";

export async function toFeedBack(imagePath, sourceData, callback) {
    //
    window.$G.getMenuId().then(MenuId => {
        //
        memnu.findById(MenuId).then(menu => {
            //
            App.DC.onEvent("COMMON_HELP_INF", {
                HELP_MODE: "",
                CMM_HELP_PAGEID: menu.bsnCode,
                CMM_HELP_PAGENM: menu.bsnName
            });
        });
    });
    const loginFlag = await checkIsLogin();
    if (loginFlag) {
        // 
        _openWebPageToFeedBack(imagePath, sourceData, callback);
    } else {
        // 
        goToLogin(null, {}, { selfRefreshFlag: true }).then(rsp => {
            if (rsp) {
                // 
                _openWebPageToFeedBack(imagePath, sourceData, callback);
            }
        });
    }
}

/**
 * 
 * webView
 */
async function _openWebPageToFeedBack(imagePath, sourceData, callback) {
    if (typeof callback === "function") {
        callback();
    }
    const floatHelpMsg = {
        path: imagePath,
        sourceData: sourceData
    };
    App.jsApi.writeToMemoryStorage({ key: "floatHelp", value: JSON.stringify(floatHelpMsg) });

    let menuId = await window.$G.getMenuId();
    if (menuId.substr(0, 6) == "MBLGN0") {
        App.jsApi.closeWebContainer();
    }
    App.jsApi.openWebPageByWebContainer({
        url: "index.html#Setting/Setting/Feedback/floatHelp"
    });
}
