import { View } from "@ehas/core";
import bankAnnounce from "./templates/index.vhtml";
import Swiper from "swiper";
export let option = {
    className: "ehas-bankAnnouncement"
};
class BankAnnounAlert extends View {
    template = bankAnnounce;
    data = () => {
        return {
            //
            bankList: [],
            //
            showBankFlg: false,
            //
            detailMessage: "",
            //
            activeIndex: 0,
            //
            showBanklist: []
        }
    };
    computed = {

    }
    methods = {
        async init() {
            this.request("msg/msg/msgancmqry.biz", {
                data: { ancmTyp: "03", pageNumber: "1", pageSize: "3", presFlg: 'Y' },
                slient: true // loading
            }).then(async rsp => {
                //
                this.bankList = rsp.LIST;
                this.bankList.forEach(element => {
                    //
                    element.isRead = false
                })
                //
                this.detailMessage = rsp.LIST[0]
                //
                let messageFlg = await this.isBooleanFun()
                if (messageFlg) {
                    //showBankFlgtrue
                    this.showBankFlg = true
                }
                //
                this.app.local.set('bankAnnounceMessage', { bankList: this.bankList })
                this.$nextTick(() => {
                    this.makeSwiper();
                });

            });
        },
        /**
         * 
         * Boolean
         */
        async isBooleanFun() {
            this.showBanklist = []
            try {
                const beforeList = await this.app.local.get('bankAnnounceMessage');
                //true
                if (!beforeList) {
                    this.showBanklist = this.bankList;
                    return true;
                }
                let midList = []
                //
                this.bankList.forEach(item => {
                    beforeList.bankList.forEach(element => {
                        if (item.ancmId == element.ancmId) {
                            item.isRead = element.isRead;
                        }
                    })
                    if (!item.isRead) {
                        //
                        this.showBanklist.push(item);
                    }
                })
                this.bankList = JSON.parse(JSON.stringify(this.bankList))
                beforeList?.bankList?.forEach(element => {
                    //
                    midList.push(element.ancmId)
                })
                //
                let boolMessage = this.bankList.some(element => {
                    return !midList.includes(element.ancmId)
                })
                //true
                if (boolMessage) return true
            } catch (error) {
                return false
            }

        },
        /**
         * 
         */
        makeSwiper() {
            let that = this
            new Swiper(".m-bank-announce", {
                observer: true, //swiperswiper
                observeParents: true, //swiperswiper
                slidesPerView: "auto", //slide
                // freeModeMomentumBounce: false,
                // resistanceRatio: 0, //
                spaceBetween: 7, // 
                slidesOffsetAfter: 50,
                centeredSlides: true, //
                on: {
                    slideChange: function () {
                        //
                        that.detailMessage = that.bankList[this.realIndex]
                    },
                    transitionEnd: function () {
                        that.activeIndex = this.realIndex;
                    }
                },
                pagination: {
                    el: ".m-swiper-paginations",
                    clickable: true,
                    bulletClass: 'm-bank-bullet',
                    bulletActiveClass: 'm-bank-active'
                },
                // 
                watchOverflow: true
            });
        },
        /**
         * 
         */
        gotoDetail() {
            this.showBankFlg = false;
            this.app.local.get('bankAnnounceMessage').then(async objList => {
                objList.bankList.forEach(element => {
                    if (element.ancmId == this.detailMessage.ancmId) {
                        //isRead
                        element.isRead = true
                    }
                })
                this.app.local.remove('bankAnnounceMessage')
                this.app.local.set('bankAnnounceMessage', { bankList: objList.bankList })
                window.$G.goToNext("#MessageCenter/MessageCenter/DetailAnnouncement", this.detailMessage);
            })

        },
        /**
         * 
         */
        closeBankAnnounce() {
            this.$refs.bankShowHidRef = ""
            this.showBankFlg = false;
            //
            this.app.local.set('bankAnnounceMessage', { bankList: this.bankList })
        }
    }
}
export default async function install(App, ViewBase) {
    let viewOption = await new BankAnnounAlert()._transformParam();
    let bankAnnoun = ViewBase.extend(viewOption);
    ViewBase.prototype.$bankAnnounceComp = function () {
        let tempContainer = document.createElement("div");
        document.body.appendChild(tempContainer);

        let bankAnnounce = new bankAnnoun({
            el: tempContainer
        });

        setTimeout(() => {
            bankAnnounce.init();
        });

        return bankAnnounce;
    };
}