import jsApi from "@ehas/app-jsapi";import { pictureJump } from "~/common/utils/ad";import { onPause as loginOnPause, onResume as loginOnResume } from "~/common/utils/login.js";import { getUrlParams } from "~/common/utils/https.js";import { onPageResume, CopyTextCheck, getClipboardText, setWai } from "~/common/utils/device";import { getPopupWindow } from "~/common/utils/dialog";import { globalUtil } from "~/common/js/globalUtil";import { transformPushMessage, pullMessage, messageJump, setMessageAsRead } from "~/common/js/message";import menu from "~/common/datas/menu";import { ViewBase } from "@ehas/core";import agreement from "~/common/datas/agreement";import warmTip from "~/common/datas/warmTip";// import { LoanUtil } from "~/common/js/loanUtil";import { toFeedBack } from "~/common/utils/feebBack.js";import { isNewStartup, isHomeView } from "~/common/utils/check";import { CustomEvent } from "~/common/utils/customEvent.js";import { versionSwitch } from "~/common/utils/versionSwitch";/* eslint-disable */let landScapeShake = "";// async function updateTipsAndAgreements(result) {    const type = result.t;    if (type === "o3") agreement.getData(true); //     if (type === "o2") warmTip.getData(true); //     //     if (type === "o1") {        try {            // const userInfo = await App.local.get("m_user_info");            // const { signPhoneNo } = userInfo || {};            const signPhoneNo = await App.local.get("loginUserNm");            const adInfo = await App.requester.request("cmm/opcl/opclmanage.biz", {                data: { signPhoneNo: signPhoneNo }            });            App.jsApi.saveAdInfoToStorage({ adInfo: adInfo.hashMap, intrvDays: adInfo.intrvDays });        } catch (error) {            window.$G.logErrorWithCode("src/config/jsapiSetting.js", JSON.stringify(error));        }    }}/** *  */async function checkNativePulldown() {    try {        const jsApiList = ["enablePulldownRefresh", "disablePulldownRefresh", "closePullRefresh"];        const rsp = await App.jsApi.jsapiExistInApp({ jsApiList });        return jsApiList.every(api => rsp.result[api]);    } catch (error) {        globalUtil.logWarnWithCode("src/config/jsapiSetting.js", "");        return false;    }}// APPurlfunction urlWhiteListCheck(skpLinkAdr, sendType = "") {    return App.requester.request("wlh/share/shareurlcheckqry.biz", { data: { skpLinkAdr, sendType } });}// uuidurlfunction getShrLinkAdr(uuid) {    return App.requester.request("wlh/share/shrlinkadrqry.biz", {        data: {            uuid: uuid        }    });}//webview Appfunction exitApp() {    ViewBase.prototype.$confirm({        message: "",        sureText: "",        cancelText: "",        closed(isSure) {            if (isSure) {                App.jsApi.exitApp();            }        }    });}async function initSafeKeyboardShock() {    const rootFlag = await App.jsApi.isRootView(); // webview    if (rootFlag && App.bank.flags?.setting?.isShowSafeKeyboardShock) {        const isFirstStart = await App.jsApi.getNativeInfo();        if (isFirstStart.result?.nativeInfo?.clientInfo?.isfirst == "Y") {            App.jsApi.jsapiExistInApp({ jsApiList: ["setSafeKeyboardShock"] }).then(res => {                if (res.result?.setSafeKeyboardShock) {                    App.jsApi.setSafeKeyboardShock({ isShock: true });                }                App.requester.request("cmm/keyboard/keyboardstschg.biz", {                    data: {                        type: "1"                    },                    slient: true //                });            });        }    }}/** *  * @param {*} data */function landScapeOnPageResume(data) {    if (isNewStartup() && App.jsApi.isRootView()) {        landScapeShake = false;        const ee = new CustomEvent();        ee.emit("customEventPageResume", data);        if (data?.result?.components == "shake") {            if (data.result.name == "set") {                window.App.menu.go("MBUSR020000");            } else if (data.result.name == "clickjump") {                const item = data.result.data;                if (item.id) {                    if (item.id == "MBECM030000") {                        window.$G.getMenuId().then(MenuId => {                            if (MenuId == "MBECM030000") {                                App.jsApi.changeTabbar({ bsnCode: "MBECM030000" });                            } else {                                window.App.menu.go(item.id);                            }                        });                    } else {                        window.App.menu.go(item.id);                    }                } else {                    versionSwitch();                }            }        }    }}let onResume = false;let onTabbarChangeFlag = false;export default {    beforeStart: async function(App) {        App.mbank = {};        App.jsApi = jsApi;        //         App.jsApi.back(() => {            const dialog = document.querySelectorAll("div[dialog-mark]"); //            if (dialog.length >= 1) {                //                return;            }            //             const isScreenFlag = App.getViewInstance("floatHelp") || {};            if (Object.keys(isScreenFlag)?.length != 0 && !isScreenFlag.isScreen) {                return;            }            //             const subPage = App.getViewInstance("subPage") || {};            if (subPage.active && typeof subPage.onBack === "function") {                subPage.onBack();                return;            }            //             const contentView = App.getViewInstance("content") || {};            const { pdfInstance } = contentView;            if (pdfInstance) {                const instanceKeys = Object.keys(pdfInstance).filter(key => pdfInstance[key].visible);                if (instanceKeys.length) return instanceKeys.forEach(key => pdfInstance[key].hide());            }            const { historyList = [] } = App.router || {};            const rootFlag = App.jsApi.isRootView(); // webview            const isAndroid = App.jsApi.isInAndroid();            if (historyList.length >= 1) {                const navBar = App.getViewInstance("navBar") || {};                /**                 *                  * 1.                 * 2.webviewAppwebview                 * 3.                 */                if (navBar.iconLeft) {                    navBar.iconLeft();                } else if (                    (App.globalFlag.simpleFlag === "Y" && App.router.fragmentUrl === "SimpleHome/Home/Index") ||                    App.router.fragmentUrl === "Huinong/Home/Index"                ) {                    //                    if (rootFlag) {                        isAndroid && exitApp();                    } else {                        App.jsApi.closeWebContainer();                    }                } else {                    //                     if (isNewStartup() && rootFlag) {                        isAndroid && exitApp();                    } else {                        App.router.back();                    }                }            } else {                /**                 *                  * 1.webviewApp                 * 2.webview                 */                if (rootFlag) {                    isAndroid && exitApp();                } else {                    App.jsApi.closeWebContainer();                }            }        });        App.jsApi.on("onAdClick", function(data) {            //             const { advgNo, advgTtl } = data.result;            App.DC.onEvent("OPCL0403", {                OPCL_ADVG_NO: advgNo,                OPCL_ADVG_TTL: advgTtl            });            App.mbank.adFlag = true;            pictureJump(data.result);        });        // app        App.jsApi.on("onPause", function() {            loginOnPause();        });        // app        App.jsApi.on("onResume", data => {            onResume = true;            setTimeout(() => {                onResume = false;            });            loginOnResume();            CopyTextCheck();            getClipboardText();            // 800P_wangenzhrouter:            App?.region?.triggerGlobalEvent("router:onResume", data);        });        //        App.jsApi.on("onPageResume", data => {            //             landScapeOnPageResume(data);            if (App.jsApi.isInAndroid() && onResume) {                // apponResumeonPageResume                // zhangguangqian                onResume = false;                return;            }            //             setWai();            if (data?.result?.loginStatus) {                //                 App.globalFlag.closeLoginStateDate = data.result;            }            if (data?.result?.themeId) {                // webview,                if (App.jsApi.isInApp()) {                    App.theme.setUserThemeId(data.result.themeId);                }            }            if (onTabbarChangeFlag) {                // tabonTabbarChangeonPageResume                onTabbarChangeFlag = false;                return;            }            if (data?.result?.preloadWebpageClose) {                // webrouter:afteronPageResume                App?.region?.triggerGlobalEvent("router:after", data);                App?.region?.triggerGlobalEvent("router:onPageResume", data);                return;            }            //             onPageResume(data);            //             App?.region?.triggerGlobalEvent("router:onPageResume", data);        });        //        App.jsApi.on("onVoiceRecordResult", data => {            App?.region?.triggerGlobalEvent("router:onVoiceRecordResult", data);        });        //        App.jsApi.on("onVoiceDecibelChanged", data => {            App?.region?.triggerGlobalEvent("router:onVoiceDecibelChanged", data);        });        //        App.jsApi.on("onVoiceRecordStart", data => {            App?.region?.triggerGlobalEvent("router:onVoiceRecordStart", data);        });        //        App.jsApi.on("onScreenShot", data => {            window.$G.goToNext("#Setting/Setting/Feedback", data.result);        });        //         App.jsApi.on("onVideoBankHangup", data => {            App?.region?.triggerGlobalEvent("router:onVideoBankHangup", data);        });        //        App.jsApi.on("onForbidSrceenShot", data => {            //            App?.region?.triggerGlobalEvent("onScreenEvent", data);            //ios            if (App.jsApi.isInIOS()) {                //type 1 2                let type = data.type || data.result.type; //                let screenType = type == "1" ? "" : "";                ViewBase.prototype.$toast(                    `${screenType}${screenType}${screenType}`                );            }        });        //         App.jsApi.on("onScheme", async data => {            //            if (data.result?.param == "EMAS_CLIENT_SHORTCUT") {                //                 App.menu.go(data.result.uri);            } else {                // App                App.mbank.schemeFlag = true;                let param = data.result.param;                let sharedparam = data.result.sharedparam;                let shrJumpTyp; // uuid7                let shrLinkAdr; // uuid                let ifUuid = false; // uuid                let uri = data.result.uri; // uri                let expdParamCntnt = {};                try {                    expdParamCntnt = JSON.parse(decodeURIComponent(sharedparam))?.expdParamCntnt;                } catch (err) {                    window.$G.logErrorWithCode("src/config/jsapiSetting.jsAPP", sharedparam);                    expdParamCntnt = "";                }                // uuidurl                if (expdParamCntnt.uuid) {                    let ret = await getShrLinkAdr(expdParamCntnt.uuid);                    shrLinkAdr = ret.shrLinkAdr;                    shrJumpTyp = ret.jumpTyp;                    ifUuid = true;                    sharedparam = getUrlParams(shrLinkAdr.split("#")[0], "shareparam");                    try {                        sharedparam = decodeURIComponent(sharedparam);                        expdParamCntnt = JSON.parse(sharedparam)?.expdParamCntnt;                    } catch (err) {                        window.$G.logErrorWithCode("src/config/jsapiSetting.jsAPP", sharedparam);                        expdParamCntnt = "";                    }                }                // uriparamparamexpdParamCntnt                if (uri && uri != "undefined" && param && !sharedparam) {                    try {                        expdParamCntnt = Object.assign({}, expdParamCntnt, JSON.parse(param));                    } catch (err) {                        window.$G.logErrorWithCode(                            "src/config/jsapiSetting.jsuriparamparamexpdParamCntnt,param",                            param                        );                    }                }                // uriexpdParamCntnt                if (uri && uri != "undefined") {                    expdParamCntnt.pgNo = uri;                }                const jumpTyp = expdParamCntnt ? expdParamCntnt.jumpTyp : ""; //                 const paramType = expdParamCntnt ? expdParamCntnt.paramType : ""; //  1  2url                 let pgNo = expdParamCntnt ? expdParamCntnt.pgNo : ""; //                 let URI = expdParamCntnt ? expdParamCntnt.URI : ""; // expdParamCntnt.URI = "loan"                // APP6.0                 if (Number(jumpTyp) === 3) {                    //                     if (App.globalFlag.simpleFlag === "Y" && pgNo === "MBFUN050100") {                        return;                    }                    // APP6.0                    if (Number(paramType) === 1) {                        // expdParamCntnt.valueJSON                        if (expdParamCntnt.value) {                            try {                                menu.findById(pgNo).then(rsp => {                                    window.$G.goToNext(rsp.url, JSON.parse(expdParamCntnt.value));                                });                            } catch (err) {                                window.$G.logErrorWithCode(                                    "src/config/jsapiSetting.jsexpdParamCntnt.valueJSONexpdParamCntnt.value",                                    expdParamCntnt?.value                                );                            }                        } else {                            //                             menu.findById(pgNo).then(rsp => {                                window.$G.goToNext(rsp.url, { ...expdParamCntnt });                            });                        }                    }                    // APP6.0                    else if (Number(paramType) === 2) {                        menu.findById(pgNo).then(rsp => {                            window.$G.goToNext(rsp.url);                        });                    } else {                        //                         menu.go(pgNo);                    }                }                // cordava                if (Number(jumpTyp) === 4) {                    menu.go(pgNo, decodeURIComponent(expdParamCntnt.paramString));                }                // H5                if (URI && URI.indexOf("loan") !== -1) {                    let item = expdParamCntnt ? expdParamCntnt : {};                    if (item.jumpTyp !== "4") {                        let { inqRslt } = await urlWhiteListCheck(item.pgNo, "loan");                        if (inqRslt === "N") {                            return;                        }                    }                    LoanUtil.gotoLoanProduct(item);                }                // 5.0                if (Number(jumpTyp) === 5) {                    let { inqRslt } = await urlWhiteListCheck(pgNo, "");                    if (inqRslt === "N") {                        return;                    }                    App.jsApi.openWebPageByJSBContainer({ url: pgNo, title: expdParamCntnt?.title || "" });                }                // APP                if (Number(jumpTyp) === 6) {                    let { inqRslt } = await urlWhiteListCheck(pgNo, "");                    if (inqRslt === "N") {                        return;                    }                    App.jsApi.openWebPageByWebContainer({ url: pgNo });                }                // uuid                if (shrJumpTyp && Number(shrJumpTyp) === 7 && ifUuid) {                    let appId = getUrlParams(shrLinkAdr, "appId");                    let isHiddenNavigationBar = getUrlParams(shrLinkAdr, "isHiddenNavigationBar");                    let { inqRslt } = await urlWhiteListCheck(pgNo, "");                    if (inqRslt === "N") {                        return;                    }                    menu.go(                        { url: pgNo, jumpType: shrJumpTyp },                        { appId: appId, isHiddenNavigationBar: isHiddenNavigationBar }                    );                }                // uuid                if (Number(jumpTyp) === 7 && !ifUuid) {                    let appId = getUrlParams(pgNo, "appId");                    let isHiddenNavigationBar = getUrlParams(pgNo, "isHiddenNavigationBar");                    menu.go(                        { url: pgNo, jumpType: jumpTyp },                        { appId: appId, isHiddenNavigationBar: isHiddenNavigationBar }                    );                }                //                 if (sharedparam) {                    App.jsApi.writeToMemoryStorage({ key: "sharedparam", value: sharedparam });                }            }        });        //  web h5        App.jsApi.on("onPushReceive", data => {            //             const rootFlag = App.jsApi.isRootView();            //             if ((!isNewStartup() && rootFlag) || (isNewStartup() && isHomeView())) {                const { result = {} } = data;                const channel = result.ch;                // ()                if (channel) return updateTipsAndAgreements(result);                //                 transformPushMessage(result);            }        });        // web          App.jsApi.on("onNotifyClick", async data => {            //             const rootFlag = App.jsApi.isRootView();            if ((!isNewStartup() && rootFlag) || (isNewStartup() && isHomeView())) {                //                await pullMessage(true);                //                 const taskId = JSON.parse(data.result.busData).taskId;                //data.result                await setMessageAsRead(taskId, data.result);                // ,                messageJump(data.result);            }        });        //        App.jsApi.on("onNetworkStatus", data => {            //             App.storage.remove("onNetworkStatus", data.result.networkStatus);            App.storage.set("onNetworkStatus", data.result.networkStatus);            if (!data.result.networkStatus) {                let pageUrl = ["FloorHome", "SimpleHome", "Wealth"];                // ,                 if (pageUrl.indexOf(App.router.fragment.moduleName) == -1) {                    App.jsApi.openWebPageByWebContainer({ url: "./network_exception_boot_page.html" });                } else {                    App?.region?.triggerGlobalEvent("router:onNetworkStatus", data);                }            } else {                App?.region?.triggerGlobalEvent("router:onNetworkStatus", data);            }        });        // SDK        App.jsApi.on("onDXRiskNotification", data => {            const action = data.result.action;            if (action == "logout") {                App.jsApi.exitApp();            }        });
//EHAS_RESULT_BRANCH_START false 

//EHAS_RESULT_BRANCH_END 
        //         let shake;        App.jsApi.on("onDeviceShaking", async function() {            const shakeCheck = await App.local.get("shakeCheck");            const osVer = App.jsApi.getOsVer();            const isAndroid = App.jsApi.isInAndroid();            // 7.1.1             if (isAndroid && osVer === "7.1.1" && shakeCheck !== "1") {                return;            } else if (shakeCheck === "0") {                //                 return;            }            let length = getPopupWindow();            const { moduleName } = App.router?.fragment;            if (!shake && length < 1 && moduleName && moduleName != "Login") {                const rootFlag = await App.jsApi.isRootView();                // webview                if (isNewStartup() && rootFlag && App.globalFlag.simpleFlag != "Y" && App.globalFlag.huinong != "Y") {                    if (!landScapeShake) {                        landScapeShake = true;                        const MenuId = await window.$G.getMenuId();                        await App.jsApi.openWebpageByPreload({                            url:                            "#LandScape/Index/Index?param=" +                            encodeURIComponent(                                JSON.stringify({                                    param: {                                        type: "04"                                    }                                })                            ),                            type: "2",                            bsnCode: MenuId                        });                    }                } else {                    if (typeof ViewBase.prototype?.$shake === "function") {                        shake = ViewBase.prototype?.$shake({                            closed: () => {                                shake = "";                            }                        });                    }                }            }        });        //key        App.jsApi.on("onUkeyLoading", data => {            App?.region?.triggerGlobalEvent("router:onUkeyLoading", data);        });        //        App.jsApi.on("onUnionpayBankBusiness", rsp => {            if (rsp?.result?.code) {                menu.go(rsp.result.code);            }        });        //         App.jsApi.on("onFeedback", rsp => {            const imageBase64String = rsp.result.imageBase64String;            const imagePath = rsp.result.imagePath;            toFeedBack(imagePath, imageBase64String, () => {});        });        // h5        App.jsApi.on("onJumpNetworkConfig", rsp => {            App.jsApi.openWebPageByWebContainer({ url: "./network_exception_boot_page.html" });        });        // tabH5        App.jsApi.on("onTabbarChange", async data => {            /**             *  link             */            if (isNewStartup() && App.jsApi.isInApp()) {                //id                const localThemeId = (await App.local.get("m_theme_id")) || "";                //link()                const linkEl = document.querySelector("#floor-theme-style");                //                  if ((localThemeId || linkEl) && !linkEl?.classList?.contains(localThemeId)) {                    //                    App.theme.setUserThemeId(localThemeId || "0");                }            }            onTabbarChangeFlag = true;            // tabwebrouter:after"router:onPageResume"tab            App?.region?.triggerGlobalEvent("router:after", data);            App?.region?.triggerGlobalEvent("router:onPageResume", data);        });        // H5        App.jsApi.on("onPlaySuitable", rsp => {            window.$wai?.Reset();        });        //         App.jsApi.on("onFloatWindowClick", rsp => {            const { type, params } = rsp.result;            //             // 0  1             if (type == 1) {                if (params.opclId) {                    App.DC.onEvent(params.opclId, {                        OPCL_ADVG_NO: params.advgNo || "",                        OPCL_ADVG_TTL: params.advgTtl || ""                    });                }                pictureJump(JSON.parse(params));            }        });    },    afterStart: async function(App) {        App.jsApi = jsApi;        const enableLogOutputFlag = !ehasProcessIsProduct;        if (App.router && App.router.fragment) {            App.jsApi.initJsapi({                enableLogOutput: enableLogOutputFlag,                beforeExec: function(param) {                    param.serveName = App.router.fragment.moduleName;                    param.currentPageRouter = App.router.fragmentUrl;                    return param;                }            });        }        //         globalUtil.hasNativePulldown = await checkNativePulldown();        //         initSafeKeyboardShock();    }};