/* eslint-disable max-lines *//* eslint-disable max-statements *//* eslint-disable max-lines-per-function *//* eslint-disable max-lines *//* eslint-disable no-case-declarations */import { ViewBase, Log } from "@ehas/core";import { getJson } from "@ehas/request-ajax";import { getAuthInfo } from "~/common/utils/auth";import { getBankName } from "~/common/utils/bank";import { scanQRcode } from "~/common/utils/scan.js";import { isLogin as checkIsLogin, isMenuDisabled, remoteBankMenuDisabled } from "~/common/utils/check";import { transferUrl } from "../js/filters";import { viewed } from "~/common/js/viewed.js";import { globalUtil } from "../js/globalUtil";import { clientVersionCom } from "~/common/utils/device";import { toNumVersion } from "~/common/utils/format.js";import { goToLogin, dealLogin } from "~/common/utils/login.js";import agreement from "../datas/agreement";import { getLocalUserInfo, checkCustomerAuthInfo } from "~/common/utils/user";import { WapWeChatUtil } from "~/common/js/wap";import simpleMenuIdDc from "~/common/datas/simpleMenuIdDc"; // - const BANK_MENU_JSON = "bankMenuJsonFileCache";const MAX_DELAY_TIME = 2000;const randomVer = () => `?v=${Date.now()}`;const bankMenuWithVer = () => {    let bank = App.bank.flags.common.bankId;    return `${ehasProcessCDNServer}/public/${bank}/ecms/menus/A03/bankMenu.json${randomVer()}`;};const JSONParse = str => {    try {        str = JSON.parse(str);    } catch (e) {        // do nothing    }    return str || {};};export default {    data: undefined,    delay: null,    getDataPromise: null,    // ()    async getData() {        // app        let isInApp = App.jsApi.isInApp();        if (isInApp) {            //             this.data = await App.local.get(BANK_MENU_JSON);        }        if (this.data) {            // JSON            this.getJsonFun(bankMenuWithVer())                .then(res => {                    this.getJsonSuccess(res);                })                .catch(e => {                    window.$G.logErrorWithCode("common/datas/menu.js", JSON.stringify(e));                });        } else {            // JSON            try {                let res = await this.getJsonFun(bankMenuWithVer());                this.getJsonSuccess(res);            } catch (e) {                window.$G.logErrorWithCode("common/datas/menu.js", JSON.stringify(e));            }        }        return this.data || [];    },    //     getJsonFun: async url => {        // app        try {            let isInApp = App.jsApi.isInApp();            if (isInApp && 0) {                let res = await App.jsApi.getDecryptedConfigFileInfo({ url });                return {                    data: JSON.parse(res.result.contentString)                };            } else {                let res = await getJson(url);                return res;            }        } catch (error) {            window.$G.logErrorWithCode("common/datas/menu.js", "" + JSON.stringify(error));            try {                let isInApp = App.jsApi.isInApp();                if (isInApp && 0) {                    let res = await App.jsApi.getDecryptedConfigFileInfo({ url });                    return {                        data: JSON.parse(res.result.contentString)                    };                } else {                    let res = await getJson(url);                    return res;                }            } catch (error) {                window.$G.logErrorWithCode("common/datas/menu.js", "" + JSON.stringify(error));            }        }    },    //     getJsonSuccess(res) {        let dict = (this.dict = res.data.menus);        //         this.data = this._formatData(dict);
//EHAS_RESULT_BRANCH_START true 
        this.getTestData(dict);
//EHAS_RESULT_BRANCH_END 
        //         App.local.set(BANK_MENU_JSON, this.data);    },    //     requestData() {        if (this.data) return this.data;        this.getDataPromise = this.getDataPromise || this.getData();        return this.getDataPromise;    },
//EHAS_RESULT_BRANCH_START true 
    // App    async getTestData(dict) {        const bank = App.bank.flags.common.bankId;        try {            const appInfoObj = await this.getJsonFun(                `${ehasProcessCDNServer}/public/999/super_app/config/${bank}.json`            );            const appInfo = appInfoObj.data;            if (appInfo) {                for (let i = 0; i < appInfo.length; i++) {                    let bsnInfo = appInfo[i].bsnInfo;                    for (let j = 0; j < bsnInfo.length; j++) {                        let { code, url } = bsnInfo[j];                        if (dict[code]) {                            dict[code].url = url;                        }                    }                }            }        } catch (error) {            Log.warn("App");        }    },
//EHAS_RESULT_BRANCH_END 
    // bsnCode    async findById(id) {        if (this.dict) return this.dict[id];        const menuList = await this.requestData();        return menuList.find(menu => menu.bsnCode == id);    },    // hash    async findByUrl(hashRouter) {        if (!hashRouter) return;        const hashArray = hashRouter.split("/");        const [moduleName, menuName, pageName = ""] = hashArray;        const router = `${moduleName}/${menuName}/${pageName}`;        const menuList = await this.requestData();        let menu = null;        //         if (hashRouter.length > 3) {            // YuEBao/Index/Detail/000719 e            menu = menuList.find(item => hashRouter === item.replacedUrl);        }        if (!menu) {            menu = menuList.find(item => router === item.replacedUrl);        }        return menu;    },    // ,     async getMenusChain(menuInfo) {        const menuList = await this.requestData();        const parentMenu = menuList.find(menu => menu.bsnCode === menuInfo.parentId);        const parentMenus = parentMenu ? await this.getMenusChain(parentMenu) : [];        return [...parentMenus, menuInfo];    },    // ()    async willGoToLogin(menu, param) {        if (menu.loginFlag == "1" && menu.jumpType != "3") {            const isLogin = await checkIsLogin();            if (!isLogin) {                goToLogin(menu.bsnCode, param, { type: 3 });                return true;            }        }    },    //     // eslint-disable-next-line complexity    async go(id, param = "", options) {        const connected = await this.checkNetWorkStatus(); //         if (!connected) return false;        if (this.delay) return;        this.delay = true;        window.clearTimeout(this.timeout);        this.timeout = setTimeout(() => {            this.delay = false;        }, MAX_DELAY_TIME);        //         let menu = {};        if (typeof id === "object") {            //             menu = id;        } else {            //              id = (await this.exchangeSimpleId(id)) || id;            menu = await this.findById(id);            const checkMenuAuthPassed = await this.checkMenuAuth(id, param, menu); //             //             if (!checkMenuAuthPassed) {                this.delay = false;                return false;            }        }        //1:,2:JSAPI,3:,4:5.0-Cordova,5:5.0-jsBrage,6:APP,7:        const { jumpType } = menu;        if (jumpType === "1") App.jsApi.openWebpageByBrowser({ url: menu.url });        if (jumpType === "2") this._openJsapi(menu);        if (jumpType === "3") this._goRouter(menu, param, options);        if (jumpType === "4") this._goCordova(menu, param || menu.param);        if (jumpType === "5") this._goJSBPage(menu, param, menu.param, options);        //app        if (jumpType === "6") this._goWebPage(menu, param || menu.param, options);        if (jumpType === "7") this._openApplet(menu, param || menu.param, options);        const resultData = { menu, param };        //         if (jumpType !== "3") {            viewed.cacheViewedHistory(resultData);        }        return resultData;    },    /**     *      */    async checkNetWorkStatus() {        const networkStatus = await App.storage.get("onNetworkStatus");        if (networkStatus === false) {            App.getViewInstance("content").$confirm({                message: "",                sureText: "",                cancelText: "",                closed: isSure => {                    if (isSure) {                        App.jsApi.openWebPageByWebContainer({ url: "./network_exception_boot_page.html" });                    }                }            });            return false;        }        return true;    },    /**     *      */    async checkMenuAuth(id, param, menu) {        //         if (!menu) throw new Error(`${id}`);        //        const menuDisabled = isMenuDisabled(menu);        if (menuDisabled) {            this.delay = false;            return;        }        // 810ios6.0.0        if (remoteBankMenuDisabled(menu.bsnCode)) {            this.delay = false;            return false;        }        //        const willGoToLogin = await this.willGoToLogin(menu, param);        if (willGoToLogin) {            this.delay = false;            return;        }        Log.info("");        // token,token        //         let MBTRN_CODE = ["MBTRN000100", "MBTRN000200", "MBTRN000300", "MBTRN000400"];        if (MBTRN_CODE.includes(menu.bsnCode)) {            let isToken = await App.jsApi                .getkeepLoginJWToken()                .then(res => {                    return res.result.token ? true : false;                })                .catch(() => {                    return false;                });            if (!isToken) {                goToLogin();                return false;            }        }        Log.info("");        // token        if (menu.jumpType != "3" || MBTRN_CODE.includes(menu.bsnCode)) {            Log.info("");            const customerAuthCheckPassed = await this.customerAuthCheck({ menu: menu });            if (!customerAuthCheckPassed) {                return false;            }        }        return true;    },    _goRouter(menu, param, options = null) {        if (menu.bsnCode == "MBTSR015300") {            // NFC            App.jsApi.jsapiExistInApp({ jsApiList: ["getNFCStatus"] }).then(res => {                if (res.result?.getNFCStatus) {                    // NFC                    App.jsApi.getNFCStatus().then(res => {                        if (res.result?.nfcStatus == "0") {                            //                             App.getViewInstance("content").$toast("NFC");                            return false;                        } else if (res.result?.nfcStatus == "1") {                            App.router.go(menu.url, { keepSelfalive: options?.keepSelfalive ? true : false });                        } else if (res.result?.nfcStatus == "2") {                            // -                            App.jsApi.setNFC();                            return false;                        }                    });                } else {                    //                    App.getViewInstance("content").$confirm({                        message: ``,                        sureText: ``,                        cancelText: "",                        modalId: "sessionOut",                        closed: isSure => {                            if (isSure) {                                //                                window.$G.goToNext("#Setting/AboutUs/Index");                            }                        }                    });                    return false;                }            });        } else {            if (param && typeof param !== "string") {                let menuUrl = menu.url;                //                //                if (param.tdType == "201" || param.tdType == "202") {                    param.from = "favorities";                    param.pdCd = param.PrdCode; //                     App.router.go(menuUrl, {                        keepSelfalive: options?.keepSelfalive ? true : false,                        pageParam: { param }                    });                    //                } else if (param.tdType == "206") {                    App.router.go(menuUrl, { keepSelfalive: options?.keepSelfalive ? true : false, pageParam: param });                } else {                    App.router.go(menuUrl, {                        keepSelfalive: options?.keepSelfalive ? true : false,                        pageParam: { param }                    });                }            } else {                App.router.go(menu.url, { keepSelfalive: options?.keepSelfalive ? true : false });            }        }        this.delay = false;    },    async _goCordova(menu, param) {        const { url } = menu;        const cdnParam = window.encodeURIComponent(`ehasProcessCDNServer=${ehasProcessCDNServer}/`);        const menusWithoutCDN = [            "174000",            "110040",            "135020",            "SFUD010400",            "SFUD010300",            "SFUD010100",            "SFUD010200",            "SFUD010500",            "105140",            "1050A0",            "105110",            "120200", //             "1D8101",            "1D8090",            "1F9000",            "SFUD020100", //            "1D8010",            "1A7000",            "119010",            "1F1000"        ]; // 5.0cdn        let agreementList = await agreement.getData(); //        let agreementData = [];        //5.0        for (let i = 0; i < agreementList.length; i++) {            if (agreementList[i].pgCd == menu.bsnCode) {                agreementData.push({                    gdCntnt: transferUrl(agreementList[i].gdCntnt),                    subPgCd: agreementList[i].subPgCd                });            }        }        agreementData = window.encodeURIComponent(`agreementData=${JSON.stringify(agreementData)}`);        // url 5.0cdn6.0        let page = "";        if (menusWithoutCDN.includes(url)) {            page = param || "";        } else {            if (!param) {                page = `${url}.html?${cdnParam}&${agreementData}`;            } else {                page = param.includes("?")                    ? `${param}&${cdnParam}&${agreementData}`                    : `${param}?${cdnParam}&${agreementData}`;            }        }                //  810-1D8101 812-1D8090 818-1F9000 817-1D8010        if (["1D8101", "1D8090", "1F9000", "1D8010", "1A7000", "1F1000"].includes(url)) {            //            const isLogin = await dealLogin();            //            if (isLogin && (await checkCustomerAuthInfo())) {                // pagepage                if (page?.page) {                    page = page.page;                }                // -: {"menuId":"1F9000","page":"1F9000.html?prdCode=SR6&cpdm=9990000209#InsuranceDetailPage"}                App.jsApi.openWebPageByCordovaContainer({ menuId: url, page: page }).finally(() => {                    // jsApiwebview                    setTimeout(() => {                        this.delay = false;                    }, 500);                });            }        } else if(App.bank.flags?.common?.socialMenuClickConfirm&&menu.bsnCode == "MBOLD1M9000"){            // 166            ViewBase.prototype.$confirm({                message: ``,                sureText: "",                cancelText: "",                closed(isSure) {                    if (isSure) {                        App.jsApi.openWebPageByCordovaContainer({ menuId: url, page: page }).finally(() => {                            // jsApiwebview                            setTimeout(() => {                                this.delay = false;                            }, 500);                        });                    }                }            });          }else {            App.jsApi.openWebPageByCordovaContainer({ menuId: url, page: page }).finally(() => {                // jsApiwebview                setTimeout(() => {                    this.delay = false;                }, 500);            });        }    },    _goJSBPage(menu, param, menuParam, options = {}) {        let jsApiOptions = {            url: menu.url,            ...options,            ...JSONParse(param),            ...JSONParse(menuParam)        };        //MBUSR030400title        if (menu.bsnCode == "MBUSR030400") {            jsApiOptions.title = " ";        }        App.jsApi.openWebPageByJSBContainer(jsApiOptions).finally(() => {            // jsApiwebview            setTimeout(() => {                this.delay = false;            }, 500);        });    },    _goWebPage(menu, param, options) {        let optionStr = "";        if (App.bank.flags.common?.goWebPageMenus?.includes(menu.bsnCode)) {            let tempArry = [];            //            if (typeof options === "object" && isNaN(options.length)) {                for (let key in options) {                    tempArry.push(key + "=" + options[key]);                }            }            if (tempArry.length > 0) {                optionStr = "?" + tempArry.join("&");            }        }        const jsApiOptions = { url: menu.url + optionStr, mchid: param };        App.jsApi.openWebPageByWebContainer(jsApiOptions).finally(() => {            // jsApiwebview            setTimeout(() => {                this.delay = false;            }, 500);        });    },    // eslint-disable-next-line max-statements    async _openJsapi(menu) {        switch (menu.url) {            case "scanQRCode":                //delay                await scanQRcode();                setTimeout(() => {                    this.delay = false;                }, 500);                break;            case "openSelectBankPage":                App.jsApi.openSelectBankPage();                break;            case "complaintPhone":                let complaintPhone = App.bank.flags?.IntelligentService?.complaintPhone;                let phoneMsg = `<p class="T16 LH24 Bd" style="text-align: center;">${complaintPhone.phoneShow}</p>`;                App.getViewInstance("content").$confirm({                    type: "t3",                    message: `<p class="T16 LH24 Bd" style="text-align: center;"></p>${phoneMsg}`,                    sureText: "",                    cancelText: "",                    closed(isSure) {                        if (isSure) {                            this.app.jsApi.openNativeApp({ url: `tel://${complaintPhone.phoneNo}` }).then(res => {                                if (res.code != "0000") {                                    // codemessage                                    window.$G.jsApiErrorMsg(res);                                }                            });                        }                    }                });                break;            case "servicePhone":                // eslint-disable-next-line no-case-declarations                const bankPhone = App.bank.flags.IntelligentService?.servicePhone;                // eslint-disable-next-line no-case-declarations                let msg = "";                if (bankPhone.length !== undefined) {                    bankPhone.forEach(item => {                        msg += `<p class="T16 LH24 Bd" style="text-align: center;">${item.phoneShow}</p>`;                    });                } else {                    msg = `<p class="T16 LH24 Bd" style="text-align: center;">${bankPhone.phoneShow}</p>`;                }                // eslint-disable-next-line no-case-declarations                let PhoneMsg = App.bank.flags.IntelligentService?.creditPhone ? "" : "";                App.getViewInstance("content").$confirm({                    type: "t3",                    message:                        // eslint-disable-next-line max-len                        `<p class="T16 LH24 Bd" style="text-align: center;">${PhoneMsg}</p>${msg}`,                    sureText: "",                    cancelText: "",                    closed(isSure) {                        if (isSure) {                            if (bankPhone.length !== undefined) {                                this.app.getViewInstance("content").$refs.servicePhone.show();                            } else {                                this.app.jsApi.openNativeApp({ url: `tel://${bankPhone.phoneNo}` }).then(() => {                                    // codemessage                                });                            }                        }                    }                });                break;            case "creditPhone":                // eslint-disable-next-line no-case-declarations                const creditPhone = App.bank.flags.IntelligentService?.creditPhone;                App.getViewInstance("content").$confirm({                    type: "t3",                    message:                        // eslint-disable-next-line max-len                        '<p class="T16 LH24 Bd" style="text-align: center;"></p><p class="T16 LH24 Bd" style="text-align: center;">' +                        creditPhone.creditPhoneShow +                        "</p>",                    sureText: "",                    cancelText: "",                    closed(isSure) {                        if (isSure) {                            this.app.jsApi.openNativeApp({ url: `tel://${creditPhone.creditPhoneNo}` }).then(() => {                                // codemessage                            });                        }                    }                });                break;            case "openWechatMiniProgram":                App.jsApi.openWechatMiniProgram(JSON.parse(menu.urlParam)).catch(rsp => {                    //codemessge                    if (rsp.code == "EPFN515T") {                        ViewBase.prototype.$alert({                            message: "",                            btnText: ""                        });                    } else {                        ViewBase.prototype.$toast(rsp.cvrtMsg);                    }                });                break;            default:                if (menu.url) {                    //                    this.openVideo(menu);                }                break;        }    },    /**     *      */    openVideo(menu) {        //        const selfBankList = ["openVideoBank"];        //        const totalVideoList = ["openVideoBankBySCCBAVB"];        if (selfBankList.includes(menu.url)) {            //            App.jsApi.openVideoBank();        }        // urlParamsdkName        let linkUrl = "";        if (menu.urlParam) {            //urlParamsdk            linkUrl = JSON.parse(menu.urlParam)?.sdkName;        }        if (totalVideoList.includes(linkUrl) || totalVideoList.includes(menu.url)) {            //            App.jsApi.openVideoBankBySCCBAVB({ url: JSON.parse(menu.urlParam).url });        }    },    _openApplet(menu, param, options = {}) {        if (menu.bsnCode == "MBFIN290000") {            //            let TypeofPhone = App.jsApi.isInIOS();            //ios6.0.056.0.0.4            const phoneVersion = TypeofPhone ? "6.0.05" : "6.0.0.4";            const clientVersion = App.jsApi.getAppVerion();            //6.0.0.0            const booleanAlert = toNumVersion(clientVersion) < toNumVersion(phoneVersion);            if (booleanAlert) {                clientVersionCom(phoneVersion);                return;            }        }        //        if (menu.bsnCode == "MBCMM030000") {            //            let TypeofPhone = App.jsApi.isInIOS();            //ios6.1.86.1.7.7            const phoneVersion = TypeofPhone ? "6.1.08" : "6.1.7.8";            const clientVersion = App.jsApi.getAppVerion();            //6.0.0.0            const booleanAlert = toNumVersion(clientVersion) < toNumVersion(phoneVersion);            if (booleanAlert) {                clientVersionCom(phoneVersion);                return;            }        }        // uk        if (param?.businessType == "V420") {            if (menu.url.indexOf("businessType") < 0) {                menu.url = menu.url + "?businessType=V420";            }        } else {            menu.url = menu.url.replace("?businessType=V420", "");        }        if (typeof param === "string") {            param = JSON.parse(param);        }        if (!param?.title && (options?.title || menu?.bsnName)) {            options.title = options.title || menu.bsnName;        }        const jsApiOptions = { url: menu.url, ...param, ...options };        App.jsApi.openApplet(jsApiOptions).catch(rsp => {            //codemessge            if (rsp.code == "EPFJ002W") {                ViewBase.prototype.$alert({                    message: "",                    btnText: ""                });            } else {                ViewBase.prototype.$toast(rsp.cvrtMsg);            }        });    },    _formatData(json) {        let data = [];        for (const key in json) {            let item = json[key];            item = {                ...item,                replacedUrl: item.url.replace(/#/, ""),                param: item.urlParam            };            data.push(item);            json[key] = item;        }        return data;    },    //   menu-panel    transMenu(menu) {        return {            id: menu.bsnCode,            text: menu.bsnName,            src: menu.icon ? transferUrl(menu.icon) : "",            rightFlag: menu.rightFlag ? transferUrl(menu.rightFlag) : "",            markLocation: menu.markLocation        };    },    // ID     async fetchMenuList(anchorCode, slientFlag) {        //         try {            if (WapWeChatUtil.isWXBusiness()) {                throw new Error("806wap");            }            const userInfo = (await getLocalUserInfo()) || {};            const { branchNo = "" } = userInfo;            const reqData = {                data: {                    body: { anchorCode, branchNo },                    header: {}                },                slient: slientFlag,                useDefaultError: false            };            const rspData = await App.requester.request("cms/MC0015.do", reqData);            const menuList = (rspData.body && rspData.body.menus) || [];            return menuList.map(menu => this.transMenu(menu));        } catch (error) {            //             try {                const { bankId } = App.bank.flags.common;                const fetchUrl = `${ehasProcessCDNServer}/public/${bankId}/ecms/menus/A03/${anchorCode}.json`;                const safeRspData = await this.getJsonFun(fetchUrl);                const safeRspMenuList = (safeRspData && safeRspData.data && safeRspData.data.menus) || [];                return safeRspMenuList.map(menu => this.transMenu(menu));            } catch (error) {                globalUtil.popErrorMsg(error);                return [];            }        }    },    /**     *      */    async permissionCheck({ routerUrl, menuInfo } = {}) {        //         if (isMenuDisabled(menuInfo)) {            return false;        }        // 810ios6.0.0        if (remoteBankMenuDisabled(menuInfo.bsnCode)) {            return false;        }        //         if (menuInfo.loginFlag === "1") {            //             const hasLogined = await checkIsLogin();            Log.info(routerUrl);            if (!hasLogined) {                goToLogin(routerUrl);                return false;            }            //             if (menuInfo.customerAuth) {                try {                    return this.customerAuthCheck({ menu: menuInfo });                } catch (error) {                    return false;                }            }        }        return true;    },    /**     *      * @param  {Object}  menu      * @param  {Boolean} closeProgressBar      */    async customerAuthCheck({ menu } = {}) {        if (WapWeChatUtil.isWXBusiness()) return true;        if (menu?.loginFlag == "1" || menu?.loginFlag == "2") {            const userLevel = {};            const userInfo = await App.local.get("m_user_info");            const localUserInfo = await getLocalUserInfo();            let mCardTypeName = App.bank.flags?.account?.mCardTypeNameFlag ? "" : "";            let level = userLevel?.result?.m_userLevel || localUserInfo?.userLevel;            if (menu.customerAuth == "C" && userInfo?.hasTkgDwnCcrdFlg == "N") {                ViewBase.prototype.$confirm({                    message: `${getBankName() + mCardTypeName}`,                    sureText: "",                    cancelText: "",                    closed(isSure) {                        if (isSure) {                            window.$G.goToNext("#Account/MyAccount/MyAcctList");                        } else {                            window.$G.backMktplatWeb();                        }                    }                });                return false;            } else if (parseInt(level) < parseInt(menu.customerAuth)) {                const authInfo = getAuthInfo(menu);                ViewBase.prototype.$confirm({                    message: authInfo.title,                    sureText: authInfo.sureBtnTxt,                    cancelText: authInfo.cancelBtnTxt,                    closed(isSure) {                        if (isSure) {                            if (authInfo.sureBtnUrl != "") {                                window.$G.goToNext(authInfo.sureBtnUrl);                            }                        } else {                            //                             window.$G.backMktplatWeb();                        }                    }                });                return false;            } else {                return true;            }        }        // true        return true;    },    /**     *      * @param  {Object}  routerInfo      */    async childIntercept(routerInfo = {}) {        if (            routerInfo.newFragment.moduleName === "ChildCard" &&            (routerInfo.newFragmentUrl === "ChildCard/Task/Task" ||                routerInfo.newFragmentUrl === "ChildCard/Finance/Hold" ||                routerInfo.newFragmentUrl === "ChildCard/Index/Manage" ||                routerInfo.newFragmentUrl === "ChildCard/LuckyMoney/LuckyMoney")        ) {            //            let { babyCardBindQryEntityList: list } = await App.requester.request("dep/babycard/babycardbindqry.biz");            if (!list.length) {                App.getViewInstance("content").$confirm({                    message: ``,                    cancelText: ``,                    sureText: ``,                    closed: isSure => {                        if (isSure) {                            //                            window.$G.goToNext("#ChildCard/Binding/Binding", { backRouter: location.hash });                        }                    }                });                return false;            }        }        return true;    },    /**     *      *  1         *  2         * @param {*} id      */    async exchangeSimpleId(id) {        //         if (App.globalFlag.simpleFlag !== "Y") return "";        //         const simpleId = simpleMenuIdDc[id];        //         if (simpleId && (await this.findById(simpleId))) {            return simpleId;        }        //         return "";    }};