import { ViewBase } from "@ehas/core";import Nprogress from "nprogress";export default {    install() {        // A->B: A(routerbefore)B(routerafter)        // eslint-disable-next-line max-statements        ViewBase.prototype.$triggerProgressBar = (status, routerInfo = {}) => {            const navBarSelector = "#nav-bar";            const navBar = document.querySelector(navBarSelector);            if (!navBar) return; //             const options = { showSpinner: false, parent: navBarSelector };            const { newFragment = {} } = routerInfo; //             const modulesWithoutNavBar = ["FloorPage", "FloorHome", "SimpleHome", "Huinong"]; // 
//EHAS_RESULT_BRANCH_START false 

//EHAS_RESULT_BRANCH_END 
            // AB()A            // DOM            if (status === "start" && !modulesWithoutNavBar.includes(newFragment.moduleName)) {                Nprogress.configure(options);                Nprogress.start();                return;            }            if (status === "done") {                const routerList = App.router.historyList;                const routerLen = routerList.length;                const prevRouter = routerList[routerLen - 2];                const configuredInCurrentPage =                    (!prevRouter || modulesWithoutNavBar.includes(prevRouter.url.split("/")[0])) &&                    !modulesWithoutNavBar.includes(App.router.fragment.moduleName);                if (configuredInCurrentPage) {                    // A()B                    // AB()                    Nprogress.configure(options);                    Nprogress.start();                    setTimeout(() => {                        if (document.querySelector(navBarSelector)) Nprogress.done();                    }, 200);                } else {                    if (document.querySelector(navBarSelector)) Nprogress.done();                }            }        };    }};