    /** --- --begin--- **/
    //
    let afterStartBeginTime = "";
    App.listener.once("after_beforeStart", () => {
        console.info(`${dateUtils.nowDate("HH:mm:ss.SSS")}mbank-H5Application beforeStart`);
        globalUtil.pushStartupTimeLine?.("App-beforeStart", "App beforeStart", startTime);
        afterStartBeginTime = globalUtil.getCurrentTime();
    });
    App.start().then(async () => {
        globalUtil.pushStartupTimeLine?.("App-afterStart", "App afterStart", afterStartBeginTime);
        console.info(`${dateUtils.nowDate("HH:mm:ss.SSS")}mbank-H5Application.then()`);
        globalUtil.pushStartupTimeLine?.("newApplication", "Application", startTime);
        import("./common/resource/common_diff.less");
        document.title = `${App.bank.flags.common.bankName}`;
        let wai = require("~/config/wai.js");
        window.$wai = wai(App.jsApi);

        // 
        let customFontSize = await App?.local?.get("customFontSize");
        if (customFontSize?.value) {
            setFontSize(customFontSize?.value);
        }
        if (App.jsApi.isRootView()) {
            App.local?.remove("requst_uuid");
        }
    });
    /** --- --end--- **/
