import { ViewBase, View } from "@ehas/core";
import template from "./templates/Index.vhtml";
import { WapWeChatUtil } from "~/common/js/wap";
// TODO:pdfjs-dist2.5+webpackios10.2
// 2.3.200jsApi1.9.426
const pdfjsLib = require("pdfjs-dist");
// pdfjs-dist/build/cmaps.bcmapCDNpdfjs
const cMapUrl = `${ehasProcessCDNServer}/public/999/h5/mbank/Common/cmaps/`;

const config = {
    defaultPropsValue: {
        hasRestpdf: "",
        url: "",
        title: "",
        timer: 10000,
        forceEveryTime: true,
        hideBtnAfterRead: false,
        buttonTextConf: "",
        confirm: () => {},
        cancel: () => {}
    }
};

// loading
let loadingClose = null;

class MPdf extends View {
    name = "m-pdf";
    template = template;

    props = {
        //pdf
        hasRestpdf: {
            type: String,
            default: config.defaultPropsValue.hasRestpdf
        },
        // PDFurl
        url: {
            type: String,
            default: config.defaultPropsValue.url
        },
        // 
        title: {
            type: String,
            default: config.defaultPropsValue.title
        },
        // 
        timer: {
            type: Number,
            default: config.defaultPropsValue.timer
        },
        // 
        confirm: {
            type: Function,
            default: config.defaultPropsValue.confirm
        },
        // 
        cancel: {
            type: Function,
            default: config.defaultPropsValue.cancel
        },
        // 
        forceEveryTime: {
            type: Boolean,
            default: config.defaultPropsValue.forceEveryTime
        },
        // 
        hideBtnAfterRead: {
            type: Boolean,
            default: config.defaultPropsValue.hideBtnAfterRead
        },
        hasConfirmed: {
            type: Boolean,
            default: false
        },
        // 
        buttonTextConf: {
            type: String,
            default: config.defaultPropsValue.buttonTextConf
        }
    };
    data = function() {
        return {
            visible: false,
            clock: null,
            seconds: this.timer,
            // 817
            whfundreadbook: App.bank.flags?.fund?.whfundreadbook
        };
    };
    computed = {
        disabled() {
            return Boolean(this.seconds);
        },
        buttonText() {
            if (this.seconds) {
                return `${this.seconds / 1000}`;
            } else {
                return this.buttonTextConf ? this.buttonTextConf : `${this.hasRestpdf}`;
            }
        }
    };
    methods = {
        // 
        hide(hideByConfirm) {
            this.visible = false;
            clearInterval(this.clock);
            this.clock = null;

            //  &  &  
            if (hideByConfirm && !this.forceEveryTime && this.hideBtnAfterRead) this.hasConfirmed = true;

            //  
            if (!this.seconds && this.forceEveryTime) this.seconds = this.timer;

            // 
            if (!hideByConfirm) {
                this.cancel && this.cancel();
            }
        },
        // 
        show() {
            if (!this.url) return;

            // PDF
            pdfjsLib
                .getDocument({
                    url: this.url,
                    cMapUrl: cMapUrl,
                    cMapPacked: true
                })
                .then(pdf => {
                    this.visible = true;
                    for (let i = 1; i <= pdf.numPages; i++) {
                        // PDF
                        pdf.getPage(i).then(page => {
                            const viewport = page.getViewport(2.0);
                            const canvas = document.createElement("canvas");
                            this.$refs.pagesContainer.appendChild(canvas);

                            const context = canvas.getContext("2d");
                            canvas.width = viewport.width;
                            canvas.height = viewport.height;

                            page.render({
                                viewport,
                                canvasContext: context
                            });
                        });
                    }

                    if (this.seconds && !this.clock) {
                        !this.clock && this.timerStart();
                    }
                    loadingClose(); // loading
                })
                .catch(() => {
                    loadingClose(); // loading
                    this.visible = false;
                });
        },
        // 
        clickButton() {
            this.hide(true);
            this.confirm && this.confirm();
        },
        // 
        timerStart() {
            this.seconds = this.timer;
            this.clock = setInterval(() => {
                if (this.seconds) {
                    this.seconds = this.seconds - 1000;
                } else {
                    clearInterval(this.clock);
                    this.clock = null;
                }
            }, 1000);
        }
    };

    onShow = function() {
        this.show();

        if (WapWeChatUtil.isWXBusiness()) {
            // 6.0pdf
            this.onGlobal("router:after", () => {
                try {
                    this.visible = false;
                    clearInterval(this.clock);
                    this.clock = null;
                    // eslint-disable-next-line no-empty
                } catch (error) {}
            });
        }
    };
}

ViewBase.prototype.$previewPdf = async function(options = {}) {
    // 817
    /**
     * wap,urlconfirm
     */
    if (App.bank.flags?.fund?.whfundreadbook || WapWeChatUtil.isWXBusiness()) {
        this.pdfInstance = "";
    }

    loadingClose = ViewBase.prototype.$loading(); // loding
    const propsData = { ...config.defaultPropsValue, ...options };
    const viewOption = await new MPdf()._transformParam(propsData);
    const MPdfComponent = ViewBase.extend(viewOption);
    if (!this.pdfInstance) this.pdfInstance = {};

    if (!this.pdfInstance[propsData.url]) {
        const pdfContainer = document.createElement("div");
        document.body.appendChild(pdfContainer);
        this.pdfInstance[propsData.url] = new MPdfComponent({
            el: pdfContainer,
            propsData
        });
    } else {
        this.pdfInstance[propsData.url].show();
    }
};

export default MPdf;
