/* eslint-disable max-lines-per-function */
import { View } from "@ehas/core";
import template from "./index.vhtml";
import { pictureJump } from "~/common/utils/ad";
import { arrayUtils } from "@ehas/utils";
import { isNewStartup, preloadStyleIsDialog } from "~/common/utils/check.js";

export let option = {
    className: "ehas-image-ad",
    default: {
        type: "1",
        isShowDelete: true,
        iconPosition: "t1",
        pctDlgFrq: "01"
    }
};

class MImageAd extends View {
    template = template;
    inheritAttrs = true;
    props = {
        //
        cancelText: String,
        //
        sureText: String,
        // icon 1 ->   2 -> 
        type: {
            type: String,
            default: option.default.type
        },
        //icon
        isShowDelete: {
            type: Boolean,
            default: option.default.isShowDelete
        },
        //icont1-> ; t2->
        iconPosition: {
            type: String,
            default: option.default.iconPosition
        },

        picUrl: String, //
        pctLinkUrl: String | Object, //
        pctLinkTyp: String, //
        bscode: String, //
        // 11ID
        sourceId: {
            type: String,
            default: ""
        },
        //
        params: Object,
        //id
        pageId: String,
        //
        pctDlgFrq: {
            type: String,
            default: option.default.pctDlgFrq
        },
        // 
        closed: Function,
        // 
        advgTtl: String,
        //id
        advgNo: String,
        // "Y":,"N":
        nvgFldFlg: String,
        //
        nvgFldNm: String,
        //
        selectFlg: false,
        // 
        advgDsc: {
            type: String,
            default: ""
        },
        isClose: false
    };
    data = {
        imageAdConfig: {},
        unselected: `${ehasProcessCDNServer}/public/999/h5/mbank/Common/unselected.png`,
        selected: `${ehasProcessCDNServer}/public/999/h5/mbank/Common/selected.png`
    };
    methods = {
        /**
         * 
         */
        async clickNotPromintAgain() {
            this.selectFlg = !this.selectFlg;
            const { pageId, advgNo } = this || {};
            let local = await this.getLocal();
            let localConf = local || {};
            if (this.selectFlg) {
                if (!localConf[pageId]) {
                    localConf[pageId] = {};
                }
                //7
                if (!localConf[pageId]["dontPromitAgain"]) {
                    localConf[pageId]["dontPromitAgain"] = [];
                }
                localConf[pageId]["dontPromitAgain"].push(advgNo);
                //
                arrayUtils.distinct(localConf[pageId]["dontPromitAgain"]);
            } else {
                //localConf[pageId]["dontPromitAgain"]advgNo
                arrayUtils.without(localConf[pageId]["dontPromitAgain"], advgNo);
            }
            this.setLocal(localConf);
        },
        onClickLeft() {
            this.hide();
            this.$emit("cancelClick", this);
        },
        onClickRight() {
            this.$emit("sureClick", this);
        },
        /**
         * 
         */
        // eslint-disable-next-line
        async isToPup() {
            const { pageId, pctDlgFrq, isSameToday, isSameWeek, isSameMonth, advgNo } = this || {};
            let now = new Date().getTime();

            let local = await this.getLocal();

            let localConf = local || {};

            //pageId
            let isExists = !!(localConf[pageId] && localConf[pageId][pctDlgFrq]) || false;
            //pageId
            let oldTime = isExists && localConf[pageId][pctDlgFrq];

            //pageId
            let oldAdvgNo = isExists && localConf[pageId]["advgNo"];
            let isEqualNo = oldAdvgNo === advgNo;

            // return
            switch (pctDlgFrq) {
                case "01":
                    //
                    //localConf[pageId]["dontPromitAgain"].includes(advgNo)
                    if (localConf[pageId] && localConf[pageId]["dontPromitAgain"]?.includes(advgNo)) {
                        //
                        if (isExists && isEqualNo && isSameWeek(now, oldTime)) {
                            this.handleNewStartupModalHide();
                            return;
                        } else {
                            //localConf[pageId]["dontPromitAgain"]advgNo
                            arrayUtils.without(localConf[pageId]["dontPromitAgain"], advgNo);
                        }
                    }
                    break;
                case "02":
                    //APP,app;
                    //
                    if (!(await this.isReboot())) {
                        this.handleNewStartupModalHide();
                        return;
                    }
                    break;
                case "03":
                    //
                    //
                    if (isExists && isEqualNo) {
                        this.handleNewStartupModalHide();
                        return;
                    }
                    break;
                case "04":
                    //
                    if (isExists && isEqualNo && isSameToday(now, oldTime)) {
                        this.handleNewStartupModalHide();
                        return;
                    }
                    break;
                case "05":
                    //
                    if (isExists && isEqualNo && isSameWeek(now, oldTime)) {
                        this.handleNewStartupModalHide();
                        return;
                    }
                    break;
                case "06":
                    //
                    if (isExists && isEqualNo && isSameMonth(now, oldTime)) {
                        this.handleNewStartupModalHide();
                        return;
                    }
                    break;
                default:
                    //return
                    this.handleNewStartupModalHide();
                    return;
            }

            localConf = Object.assign({}, localConf, {
                [pageId]: Object.assign({}, localConf[pageId], { [pctDlgFrq]: now, advgNo })
            });
            this.setLocal(localConf);
            this.show();
        },
        show() {
            this.$refs.modal && this.$refs.modal.show();
        },
        hide() {
            this.$refs.modal && this.$refs.modal.hide();
            this.closed && this.closed();
        },
        onClose() {
            this.hide();
            this.$emit("close");
            this.handleNewStartupModalHide();
        },
        //
        imgClick() {
            const { pctLinkUrl, hide } = this;
            // 
            hide();
            if (!pctLinkUrl) {
                return;
            }
            App.DC.onEvent("OPCL0201", {
                OPCL_ADVG_NO: this.advgNo,
                OPCL_ADVG_TTL: this.advgTtl
            });
            //
            pictureJump({
                pctLinkTyp: this.pctLinkTyp,
                pctLinkUrl: this.pctLinkUrl,
                advgTtl: this.advgTtl,
                nvgFldFlg: this.nvgFldFlg,
                nvgFldNm: this.nvgFldNm,
                sourceId: this.sourceId
            });
        },
        /**
         * 
         * @param {*} nowDate
         * @param {*} oldDate
         */
        isSameToday(nowDate, oldDate) {
            let d = new Date(oldDate);
            let todayDate = new Date(nowDate);
            return d.setHours(0, 0, 0, 0) == todayDate.setHours(0, 0, 0, 0);
        },
        /**
         * 
         * @param {*} nowDate
         * @param {*} oldDate
         */
        isSameWeek(nowDate, oldDate) {
            let now = new Date(nowDate);
            let old = new Date(oldDate);
            let oneDayTime = 1000 * 60 * 60 * 24;
            let nowCount = parseInt(now.getTime() / oneDayTime);
            let oldCount = parseInt(old.getTime() / oneDayTime);

            return parseInt((nowCount + 4) / 7) == parseInt((oldCount + 4) / 7);
        },
        /**
         * 
         * @param {*} nowDate
         * @param {*} oldDate
         */
        isSameMonth(nowDate, oldDate) {
            let now = new Date(nowDate);
            let old = new Date(oldDate);
            let nowMonth = now.getMonth();
            let oldMonth = old.getMonth();
            let nowYear = now.getFullYear();
            let oldYear = old.getFullYear();

            return nowMonth == oldMonth && nowYear == oldYear;
        },
        /**
         * local 
         */
        async getLocal() {
            let result = await this.app.local.get("m_image_ad");
            return result;
        },
        /**
         *
         * @param {*} data
         */
        setLocal(data) {
            this.app.local.set("m_image_ad", data, true);
        },

        /**
         * session
         */
        async isReboot() {
            const { pageId } = this || {};
            let session = await this.getSessionReboot();

            let sessionConf = session || {};

            if (sessionConf[pageId]) {
                return false;
            }

            sessionConf = Object.assign({}, sessionConf, {
                [pageId]: true
            });

            // session
            this.setSessionReboot(sessionConf);

            return true;
        },

        /**
         * session
         */
        async getSessionReboot() {
            let result = await this.app.storage.get("m_image_ad_reboot");
            return result;
        },
        /**
         * Session
         * @param {*} data
         */
        setSessionReboot(data) {
            this.app.storage.remove("m_image_ad_reboot");
            this.app.storage.set("m_image_ad_reboot", data, true);
        },
        handleNewStartupModalHide() {
            if (this.close) return;
            this.close = true;
            if (isNewStartup() && preloadStyleIsDialog()) {
                App.jsApi.closeWebContainer({ backData: { type: "ad" } });
            }
        }
    };
    computed = {
        iconType() {
            return this.type === "1" ? "cancel" : "search-delete";
        }
    };
}

export default async function install(App, ViewBase) {
    let viewOption = await new MImageAd()._transformParam({});
    let ImageAdComponent = ViewBase.extend(viewOption);

    ViewBase.prototype.$imageAd = function(option) {
        let tempContainer = document.createElement("div");

        document.body.appendChild(tempContainer);

        let ins = new ImageAdComponent({
            el: tempContainer,
            propsData: option
        });

        setTimeout(() => {
            ins.isToPup();
        });

        return ins;
    };
}
