import { ViewBase } from "@ehas/core";

let beginTime = "";
export default {
    /**
     * zip
     * @param {*} option {downloadZips: ["FloorHome", "FloorHome_Home"]}
     */
    beforeDownload(option) {
        if (!option.downloadZips) return;

        let showLoading = true
        for ( let i = 0; i < option.downloadZips.length; i++){
            if(option.downloadZips[i].indexOf('LandScape') !== -1) {
                showLoading = false
                break
            }
        }
        if (option.downloadZips.includes("FloorHome_Home")) {
            // zip
            beginTime = window.$G.getCurrentTime();
        } else if(showLoading) {
            // ziploading
            option.closeLoading = ViewBase.prototype.$loading();
        }
    },
    /**
     * zip
     * @param {*} option {downloadZips: ["FloorHome", "FloorHome_Home"]}
     */
    afterDownload(option) {
        option.closeLoading?.();
        if (!option.downloadZips) return;
        // zip
        if (option.downloadZips.includes("FloorHome_Home")) {
            window.$G.pushStartupTimeLine(`downloadFloorHome`, "zip", beginTime);
        }
    },
    async downloadFail(info) {
        let errInfo = info.error;
        // jsapi
        // eslint-disable-next-line no-undef
        if (ehasProcessIsProduct) errInfo.cvrtMsg = "";
        // router
        const networkStatus = await App.storage.get("onNetworkStatus");
        if (networkStatus) {
            window.$G.popErrorMsg(errInfo);
        }
    }
};
